.content-booking {
  position: relative;
  margin-top: -17px;
  padding-bottom: 50px;
  @media screen and (min-width: $desktop-small) {
    padding-bottom: 0;
  }
}

.booking-row {
  @include clearfix;
  @media screen and (min-width: $desktop-small) {
    margin-right: -15px;
    margin-left: -15px;
  }
  &-left {
    @media screen and (min-width: $desktop-small) {
      width: calc(100% - 350px);
      float: left;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  &-right {
    @media screen and (min-width: $desktop-small) {
      width: 350px;
      float: left;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

.content-main-title {
  background: $color3;
  padding: 30px 0 50px;
  border-bottom: 1px solid $color1;
  .main-title {
    text-align: center;
    position: relative;
    @media screen and (min-width: $desktop-small) {
      width: calc(100% - 342px);
    }
    .title {
      font-size: 28px;
      font-weight: bold;
      text-transform: none;
      margin: 0 0 7px;
      padding-top: 40px;
      @media screen and (min-width: $desktop-small) {
        padding-top: 0;
      }
    }
    .des {
      font-size: 16px;
      color: $red;
    }
    .btn-back {
      background: $light-grey;
      color: $white;
      font-weight: 400;
      height: 36px;
      line-height: 36px;
      padding: 0 15px;
      display: inline-block;
      vertical-align: middle;
      border-radius: 4px;
      position: absolute;
      left: 0;
      top: 0;
      i {
        margin-right: 5px;
        font-size: 11px;
        top: -2px;
        position: relative;
      }
    }
  }
}

.booking-day {
  border: 1px solid $color1;
  padding: 15px 0;
  border-radius: 4px;
  background: $grey;
  margin-bottom: 20px;
  overflow: hidden;
  .booking-slide {
    padding-right: 40px;
    padding-left: 40px;
    @media screen and (min-width: 1200px) {
      padding-right: 80px;
      padding-left: 80px;
    }
  }
  .owl-item {
    cursor: pointer;
  }
  .owl-nav {
    .owl-prev, .owl-next {
      width: 72px;
      height: 72px;
      border-radius: 50%;
      position: absolute;
      text-align: center;
      line-height: 72px;
      background: $white;
      border: 1px solid $color1;
      top: 50%;
      @include transform(translateY(-50%));
      .icon-arrow-left {
        position: relative;
        margin-left: 30px;
      }
      .icon-arrow-right {
        position: relative;
        margin-right: 30px;
      }
    }
    .owl-prev {
      left: -76px;
      @media screen and (min-width: 1200px) {
        left: -116px;
      }
    }
    .owl-next {
      right: -76px;
      @media screen and (min-width: 1200px) {
        right: -116px;
      }
    }
  }
  .slideday {
    .itemday {
      text-align: center;
      padding: 5px;
      line-height: 22px;
      &.active {
        .itemday__inner {
          border: 1px solid $color5;
          @include box-shadow(0, 0, 5px, rgba(0, 0, 0, 0.3));
        }
        .daytop {
          color: $textcolor-1;
        }
        .daymiddle {
          color: $textcolor-1;
        }
        .daybottom {
          color: $textcolor-1;
        }
      }
      &__inner {
        padding: 10px;
        border: 1px solid $color1;
        background: $white;
        @include box-shadow(0, 0, 5px, rgba(0, 0, 0, 0));
        @include transition (all .3s ease);
      }
      .daytop {
        font-size: 13px;
        font-weight: 600;
        color: $color5;
        @include transition (color .3s ease);
      }
      .daymiddle {
        font-size: 30px;
        font-weight: 600;
        color: $color5;
        @include transition (color .3s ease);
      }
      .daybottom {
        font-size: 13px;
        font-weight: 600;
        text-transform: uppercase;
        color: $color5;
        @include transition (color .3s ease);
      }
    }
  }

  .slidehours {
    .itemhour {
      text-align: center;
      padding: 5px;
      line-height: 22px;
      &.active {
        .itemhour__inner {
          border: 1px solid $color5;
          @include box-shadow(0, 0, 5px, rgba(0, 0, 0, 0.3));
        }
        .hour {
          color: $textcolor-1;
        }
      }
      &__inner {
        padding: 15px 10px;
        border: 1px solid $color1;
        background: $white;
        @include box-shadow(0, 0, 5px, rgba(0, 0, 0, 0));
        @include transition (all .3s ease);
      }
      .hour {
        font-size: 18px;
        font-weight: 600;
        color: $color5;
        @include transition (color .3s ease);
      }
    }
  }

}

.vos-coordonnees {
  border: 1px solid $color1;
  padding: 30px 30px 15px;
  border-radius: 4px;
  background: $grey;
  overflow: hidden;
  margin-bottom: 20px;
  .title {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 600;
    color: $green;
    margin: 0 0 20px;
    padding-bottom: 15px;
    border-bottom: 1px solid $color1;
    @media screen and (min-width: $tablet-medium) {
      width: 70%;
    }
    &:nth-of-type(n+2) {
      margin-top: 15px;
    }
    .small-txt {
      font-size: 20px;
      font-weight: 400;
      text-transform: none;
    }
  }
  .form {
    font-size: 14px;
    .inner {
      margin-right: -9px;
      margin-left: -9px;
      @include clearfix;
    }
    &__col {
      @include clearfix;
      @media screen and (min-width: $tablet-medium) {
        width: 50%;
        float: left;
        padding-left: 9px;
        padding-right: 9px;
      }
    }
    fieldset {
      border: 0;
      margin: 0;
    }
    &__col__full {
      @include clearfix;
      @media screen and (min-width: $tablet-medium) {
        width: 100%;
        float: left;
        padding-left: 9px;
        padding-right: 9px;
      }
    }
    .SumoSelect {
      margin-bottom: 15px;
      > .CaptionCont {
        padding-right: 20px;
        padding-left: 20px;
        font-weight: 500;
      }
    }

  }
  .form-item {
    legend {
      .fieldset-legend {
        .label-radio {
          font-weight: 500;
        }
      }
    }
    .fieldset-wrapper {
      .form-radios {
        .form-type-radio {
          display: inline-block;
          margin-right: 20px;
          margin-bottom: 15px;
          padding-left: 15px;
        }
      }
      .form-radios {
        cursor: default;
        padding-left: 4px;
      }
      .form-radios label {
        cursor: pointer;
        display: inline;
        vertical-align: top;
        position: relative;
        padding-left: 5px;
        font-size: 15px;
        font-weight: 600;
      }
      .form-radios label::before {
        cursor: pointer;
        content: '';
        display: inline-block;
        position: absolute;
        width: 17px;
        height: 17px;
        top: 2px;
        left: 0;
        margin-left: -20px;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 50%;
        background-color: #fff;
        transition: border 0.15s ease-in-out;
      }
      .form-radios label::after {
        cursor: pointer;
        display: inline-block;
        position: absolute;
        content: ' ';
        width: 11px;
        height: 11px;
        left: 3px;
        top: 5px;
        margin-left: -20px;
        border-radius: 50%;
        background: $color2;
        transform: scale(0, 0);
        transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      }
      .form-radios input[type='radio'] {
        margin-left: 0;
        cursor: pointer;
        opacity: 0;
        z-index: 1;
        position: absolute;
        width: auto;
      }
      .form-radios input[type='radio']:focus + label::before {
        outline: none;
        outline-offset: 0;
      }
      .form-radios input[type='radio']:checked + label::after {
        transform: scale(1, 1);
      }
      .form-radios input[type='radio']:disabled + label {
        opacity: 0.65;
      }
      .form-radios input[type='radio']:disabled + label::before {
        cursor: not-allowed;
      }
      .form-radios input[type='radio']:disabled + label::after {
        cursor: not-allowed;
      }
    }
  }
  .captcha{
    clear: both;
    display: block;
    padding-left: 10px;
  }
}

.group-btn {
  text-align: right;
  padding-bottom: 50px;
  @include clearfix;
  .champs {
    color: $color6;
    font-weight: 500;
    font-size: 14px;
    margin-top: 15px;
    margin-bottom: 15px;
    @media screen and (min-width: $tablet-medium) {
      float: left;
      margin-bottom: 0;
    }
  }
  .btn-envoyer {
    background: $colorpink;
    color: $white;
    font-weight: 600;
    text-transform: uppercase;
    padding: 15px;
    width: 100%;
    border-radius: 4px;
    @include transition (all .3s ease);
    @include hover {
      background: $colorpink2;
    }
    @media screen and (min-width: $tablet-medium) {
      float: right;
      width: auto;
      padding: 15px 70px;
    }
  }
}

.info-booking {
  background: $grey;
  border-radius: 6px;
  border: 1px solid $color1;
  margin-bottom: 20px;
  .link-over {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 4;
  }
  &__top {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      bottom: 0;
      left: 0;
      right: 0;
      top: 50%;
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
    }
    @include hover {
      .image {
        img {
          @include scale(1.1);
        }
      }
    }
    .image {
      position: relative;
      z-index: 1;
      overflow: hidden;
      img {
        width: 100%;
        border-radius: 6px 6px 0 0;
        @include transition (all .3s ease);
        @include scale(1);
      }
    }
    .content {
      position: absolute;
      z-index: 3;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 15px;
      color: $white;
      line-height: 18px;
      .price {
        font-weight: 300;
        font-style: italic;
        font-size: 14px;
        span {
          font-size: 18px;
          font-weight: 700;
        }
      }
      .short-des {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
  &__bottom {
    padding: 20px;
    @include clearfix;
    .info-person {
      padding-bottom: 15px;
      @include clearfix;
    }
    .info-person-detail {
      border-top: 1px solid $color1;
      padding-top: 15px;
    }
    .image {
      float: left;
      width: 48px;
      img {
        border-radius: 50%;
        border: 1px solid rgba($textcolor-1, 0.2);
      }
    }
    .des-right {
      float: left;
      width: calc(100% - 48px);
      padding-left: 15px;
      line-height: 18px;
      .publie {
        font-size: 13px;
        font-weight: 500;
        span {
          text-transform: uppercase;
          color: $green;
          font-weight: 600;
        }
      }
      .name {
        color: $blue;
        font-size: 15px;
        font-weight: 500;
      }
      .price-vote {
        font-size: 15px;
        font-weight: 600;
        @include clearfix;
        .price {
          float: left;
        }
        .vote {
          float: left;
          padding-top: 4px;
          padding-left: 10px;
          @include clearfix;
          .star {
            float: left;
            margin-right: 1px;
            color: $yellow;
            font-size: 11px;
            line-height: 12px;
          }
        }
      }
    }
    .text {
      text-align: center;
      margin-bottom: 20px;
    }
    .group-tel-btn {
      @include clearfix;
      &__left {
        float: left;
        width: 50%;
      }
      &_right {
        float: left;
        width: 50%;
        text-align: center;
      }
      .number-tel {
        font-weight: 600;
        font-size: 15px;
        margin-top: 9px;
        display: block;
        @include transition (all .3s ease);
        @include hover {
          color: $colorpink;
        }
      }
      .btn-email {
        background: $colorpink;
        color: $white;
        display: inline-block;
        border-radius: 4px;
        padding: 9px 30px;
        text-align: center;
        @include transition (all .3s ease);
        @include hover {
          background: $darkpink;
        }
      }
    }
  }
}