.header {
  background: $textcolor-1;
  height: 50px;
  position: fixed;
  //position: relative;
  left: 0;
  right: 0;
  top: 0;
  z-index: 101;
  transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  @include transition (all .3s ease);
  @include clearfix;
  &:before {
    content: '';
    background: $textcolor-1;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    height: 50px;
    width: 100%;
    display: block;
    @media screen and (min-width: $desktop-small) {
      display: none;
    }
  }
  .wrapper-fullwidth {
    padding: 0 15px;
    @media screen and (min-width: $desktop-small) {
      padding: 0;
    }
  }
  &.fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
  }
  &__logo {
    float: left;
    width: 67px;
    text-align: center;
    position: relative;
    z-index: 10;
    @media screen and (min-width: $desktop-small) {
      border-right: 1px solid $dark;
      width: 118px;
    }
    @media screen and (min-width: $desktop-medium) {
      width: 118px;
    }
    a {
      display: block;
      height: 50px;
      padding-top: 10px;
      img {
        display: block;
        margin: 0 auto;
      }
    }
  }
  &__menu {
    float: left;
    .mon-espace-mobile {
      border-bottom: 0;
      &__inner {
        .tele {
          color: $white;
          font-size: 18px;
          font-weight: 500;
          display: block;
          padding: 20px 0;
        }
        .wrap-btn-espace {
          display: block;
          .btn-espace {
            display: inline-block;
            background: $green;
            color: $white;
            font-size: 15px;
            font-weight: 500;
            height: 40px;
            line-height: 40px;
            padding: 0 105px;
            @include border-radius(5px);
            @include transition (all .3s ease);
            @include hover {
              background: $blackgreen;
            }
          }
        }
      }
    }
  }
  &__right {
    float: right;
    padding-top: 8px;
    padding-right: 80px;
    position: relative;
    z-index: 10;
    @include opacity(1);
    @include transition (opacity 0.3s ease-out);
    @media screen and (min-width: $tablet-medium) {
      padding-right: 75px;
    }
    @media screen and (min-width: $desktop-small) {
      padding-right: 30px;
    }
    .menu-mobile--active & {
      @include opacity(0);
    }
    .tele {
      color: $white;
      font-size: 16px;
      font-weight: 500;
      display: none;
      margin-right: 28px;
      @media screen and (min-width: $tablet-medium) {
        display: inline-block;
      }
    }
    .wrap-btn-espace {
      display: inline-block;
      .btn-espace {
        display: block;
        background: $green;
        color: $white;
        font-size: 0;
        font-weight: 500;
        width: 37px;
        height: 34px;
        line-height: 34px;
        padding: 0 18px;
        @include border-radius(5px);
        @include transition (all .3s ease);
        @include hover {
          background: $blackgreen;
        }
        &:before {
          content: '\e80a';
          font-family: 'fontello';
          position: absolute;
          left: 10px;
          right: 0;
          margin: 0 auto;
          font-size: 16px;
          @media screen and (min-width: $tablet-medium) {
            display: none;
          }
        }
        @media screen and (min-width: $tablet-medium) {
          width: auto;
          font-size: 15px;
        }
      }
    }
  }
}