.action-search {
  padding: 10px 15px 15px;
  background: $color3;
  position: absolute;
  z-index: 7;
  width: 100%;
  @include transition (all .3s ease);
  @include clearfix;
  @media screen and (min-width: $desktop-small) {
    padding: 10px 25px 0;
    z-index: 10;
    position: fixed;
    left: 0;
    right: 0;
    top: 50px;
  }
  &.fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 50px;
    @include box-shadow(0, 0, 10px, 0, rgba($black, 0.5));
  }
  &.active {
    z-index: 9;
    @include transition (all .3s ease);
    .type-search {
      max-height: 2000px;
      @media screen and (min-width: $desktop-small) {
        height: auto;
      }
      .inner {
        > div {
          display: block;
        }
        .type-search__plus {
          display: none;
          @media screen and (min-width: $desktop-small) {
            display: block;
          }
        }
        .type-search__alerte {
          display: none;
          @media screen and (min-width: $desktop-small) {
            display: block;
          }
        }
      }
      .innerbt {
        display: block;
        @media screen and (min-width: $desktop-small) {
          display: none;
        }
      }
      &__plus {
        .btn-de-criteres {
          .plus-minus {
            &:before {
              content: '-';
            }
          }
        }
      }
    }
    .show-hide-mobile {
      .mobile__alerte {
        .btn-alerte {
          > span {
            display: inline-block;
          }
        }
      }
    }
  }
  .search {
    height: 36px;
    border: 1px solid #e5e6e4;
    border-radius: 4px;

    @media screen and (min-width: $tablet-medium) {
      float: left;
      width: 460px;
    }
    &__select {
      .dd-container {
        .dd-select {
          height: 34px;
          line-height: 14px;
        }
      }
    }
    &__input {
      .input {
        height: 34px;
      }
      .button-search {
        height: 34px;
      }
    }
  }
  .type-search {
    max-height: 90px;
    //overflow: hidden;
    @include transition (all .5s ease);
    @include clearfix;
    //@media screen and (min-width: $tablet-medium){
    //  float: left;
    //  width: calc(100% - 460px);
    //}
    @media screen and (min-width: $desktop-small) {
      height: auto;
    }
    .inner {
      margin-right: -5px;
      margin-left: -5px;
      //padding-left: 10px;
      position: relative;
      @include clearfix;
      > div {
        display: none;
        @media screen and (min-width: $desktop-small) {
          display: block;
        }
        &:first-child {
          display: block;
        }
        &:nth-child(2) {
          display: block;
        }
      }
    }
    .innerbt {
      //height: 0;
      //overflow: hidden;
      display: none;
      //@include transition (all .3s ease);
      @media screen and (min-width: $desktop-small) {
        display: none;
      }
    }
    .dd-container {
      width: 100% !important;
      .dd-select {
        width: 100% !important;
        background: $white !important;
        border-radius: 4px;
        border: 1px solid #dddddd;
        height: 36px;
        line-height: 16px;
      }
    }
    .dd-selected {
      font-weight: 500;
      color: $textcolor-1;
      text-align: center;
      padding-right: 25px;
    }
    .dd-options {
      width: 100% !important;
    }
    &__list {
      padding-left: 5px;
      padding-right: 5px;
      position: relative;
      margin-bottom: 10px;
      @media screen and (min-width: $desktop-small) {
        float: left;
        width: 12.5%;
      }
      .sumo_option_achat{
        .CaptionCont{
          span{
            color: #00887b;
          }
        }
      }
    }
    &__list2 {
      padding-left: 5px;
      padding-right: 5px;
      position: relative;
      margin-bottom: 10px;
      @media screen and (min-width: $desktop-small) {
        float: left;
        width: 25%;
      }
    }
    &__plus {
      padding-left: 5px;
      padding-right: 5px;
      position: relative;
      margin-bottom: 10px;
      display: none;
      @media screen and (min-width: $desktop-small) {
        float: left;
        width: 12.5%;
        display: block;
      }
      .dd-container {
        .dd-select {
          background: $light-grey !important;
          border: 1px solid $light-grey;
        }
      }
      .dd-selected {
        color: $white;
      }
      .btn-de-criteres {
        background: $light-grey;
        color: $white;
        text-align: center;
        border-radius: 4px;
        display: block;
        height: 36px;
        line-height: 36px;
        font-weight: 700;
        width: 100%;
        &.active {
          .plus-minus {
            &:before {
              content: '-';
            }
          }
        }
        .plus-minus {
          display: inline-block;
          width: 15px;
          height: 5px;
          &:before {
            content: '+';
            display: inline-block;
            position: relative;
          }
        }
      }
    }
    &__alerte {
      padding-left: 5px;
      padding-right: 5px;
      position: relative;
      margin-bottom: 10px;
      display: none;
      @media screen and (min-width: $desktop-small) {
        float: left;
        width: 12.5%;
        display: block;
      }
      .btn-alerte {
        background: $red;
        color: $white;
        text-align: center;
        border-radius: 4px;
        display: block;
        height: 36px;
        line-height: 36px;
        overflow: hidden;
        width: 100%;
        text-transform: none;
        font-weight: 700;
        letter-spacing: inherit;
        i {
          margin-left: 10px;
          font-size: 14px;
        }
        &:hover{
          background: $colorpink2;
        }
      }
      .ajax-progress{
        position: absolute;
        right: 10px;
        top: 8px;
      }
    }
    .searchlien {
      position: relative;
      .inputlien {
        border-radius: 4px;
        border: 1px solid #dddddd;
        background: $white;
        height: 36px;
        line-height: 22px;
        padding: 5px 12px;
        width: 100%;
        position: relative;
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color:#757575;
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color:#757575;
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color:#757575;
        }
        &:-moz-placeholder { /* Firefox 18- */
          color:#757575;
        }
      }
      .btn-lien {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        width: 35px;
        i {
          color: #909195;
          font-size: 12px;
          font-style: normal;
          line-height: 16px;
          text-align: center;
        }
      }
    }
    .list-type-de-bien {
      .SumoSelect {
        > .optWrapper {

          @media screen and (min-width: $desktop-small) {
            width: calc(300% + 20px);
          }
          &.multiple {
            > .options {
              li {
                &.opt {

                  @media screen and (min-width: $desktop-small) {
                    width: 33.3%;
                    display: inline-block;
                  }
                }
              }
            }
          }
        }
      }
    }
    .list-commodites {
      .SumoSelect {
        > .optWrapper {

          @media screen and (min-width: $desktop-small) {
            width: calc(300% + 20px);
          }
          &.multiple {
            > .options {
              li {
                &.opt {

                  @media screen and (min-width: $desktop-small) {
                    width: 33.3%;
                    display: inline-block;
                  }
                }
              }
            }
          }
        }
      }
    }
    .btn-budget {
      color: #757575;
      border-radius: 4px;
      display: block;
      font-style: italic;
      font-weight: 500;
      border: 1px solid #ddd;
      background: $white;
      height: 36px;
      line-height: 22px;
      padding: 5px 12px;
      width: 100%;
      text-align: left;
      &:after {
        content: '\e802';
        font-family: 'fontello';
        font-size: 12px;
        color: #909195;
        position: absolute;
        top: 20%;
        right: 20px;
        z-index: 1;
      }
      &.active {
        &:after {
          content: '\e805';
        }
      }
    }
    .frame-budget {
      display: none;
      padding: 20px 10px;
      position: absolute;
      background: $white;
      z-index: 1010;
      width: calc(100% - 10px);
      border: 1px solid #e5e6e4;
      box-shadow: 0 0 20px rgba(0, 0, 0, .15);
      margin-top: 4px;
      @media screen and (min-width: $desktop-small) {
        width: calc(200% - 10px);
      }
      //&.active {
      //
      //}
      .frame-budget__inner {
        @include clearfix;
      }

      .label {
        float: left;
        width: 30px;
        line-height: 36px;
        text-align: center;
      }
      .wrap-input-price {
        float: left;
        width: calc(100% - 30px);
        .input-price {
          border-radius: 4px;
          font-weight: 400;
          border: 1px solid #ddd;
          background: $white;
          height: 36px;
          line-height: 22px;
          padding: 5px 12px;
          width: calc(100% - 20px);
        }
      }
      &__left {
        width: 50%;
        float: left;
        .wrap-input-price {
          .input-price {
            width: calc(100% - 20px);
          }
        }
      }
      &__right {
        //width: 670px;
        width: 50%;
        float: left;
      }
    }
    .btn-surface {
      color: #757575;
      border-radius: 4px;
      display: block;
      font-weight: 500;
      border: 1px solid #ddd;
      background: $white;
      height: 36px;
      line-height: 22px;
      padding: 5px 12px;
      width: 100%;
      text-align: left;
      font-style: italic;
      &:after {
        content: '\e802';
        font-family: 'fontello';
        font-size: 12px;
        color: #909195;
        position: absolute;
        top: 20%;
        right: 20px;
        z-index: 1;
      }
      &.active {
        &:after {
          content: '\e805';
        }
      }
    }
    .frame-surface {
      display: none;
      padding: 20px 10px;
      position: absolute;
      background: $white;
      width: calc(100% - 10px);
      z-index: 1010;
      border: 1px solid #e5e6e4;
      box-shadow: 0 0 20px rgba(0, 0, 0, .15);
      margin-top: 4px;
      @media screen and (min-width: $desktop-small) {
        width: calc(200% - 10px);
      }
      //&.active {
      //
      //}
      .frame-surface__inner {
        @include clearfix;
      }

      .label {
        float: left;
        width: 30px;
        line-height: 36px;
        text-align: center;
      }
      .wrap-input-price {
        float: left;
        width: calc(100% - 30px);
        .input-price {
          border-radius: 4px;
          font-weight: 400;
          border: 1px solid #ddd;
          background: $white;
          height: 36px;
          line-height: 22px;
          padding: 5px 12px;
          width: 100%;
        }
      }
      &__left {
        width: 50%;
        float: left;
        .wrap-input-price {
          .input-price {
            width: calc(100% - 5px);
          }
        }
      }
      &__right {
        width: 50%;
        float: left;
      }
    }
    .btn-surface-terrain {
      color: #757575;
      border-radius: 4px;
      display: block;
      font-weight: 500;
      font-style: italic;
      border: 1px solid #ddd;
      background: $white;
      height: 36px;
      line-height: 22px;
      padding: 5px 12px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      text-align: left;
      &:after {
        content: '\e802';
        font-family: 'fontello';
        font-size: 12px;
        color: #909195;
        position: absolute;
        top: 20%;
        right: 20px;
        z-index: 1;
      }
      &.active {
        &:after {
          content: '\e805';
        }
      }
    }
    .frame-surface-terrain {
      display: none;
      padding: 20px 10px;
      position: absolute;
      background: $white;
      width: calc(100% - 10px);
      z-index: 1010;
      border: 1px solid #e5e6e4;
      box-shadow: 0 0 20px rgba(0, 0, 0, .15);
      margin-top: 4px;

      .wrap-input-price {
        width: 100%;
        .input-price {
          border-radius: 4px;
          font-weight: 400;
          border: 1px solid #ddd;
          background: $white;
          height: 36px;
          line-height: 22px;
          padding: 5px 12px;
          width: 100%;
        }
      }
    }

  }
  .show-hide-mobile {
    display: block;
    margin-top: 5px;
    @media screen and (min-width: $desktop-small) {
      display: none;
    }

    .mobile__plus {
      position: relative;
      display: inline-block;
      .btn-de-criteres {
        background: $light-grey;
        color: $white;
        text-align: center;
        border-radius: 4px;
        display: block;
        height: 36px;
        line-height: 36px;
        font-weight: 400;
        width: 100%;
        padding: 0 5px;
        @media screen and (min-width: $tablet-small) {
          padding: 0 25px;
        }
        &.active {
          .plus-minus {
            &:before {
              content: '-';
            }
          }
          .spanplus {
            display: none;
          }
          .spanmoins {
            display: block;
          }
        }
        .plus-minus {
          display: inline-block;
          width: 15px;
          height: 5px;
          //margin-left: 10px;
          &:before {
            content: '+';
            //font-family: 'fontello';
            display: inline-block;
            position: relative;
          }
        }
        .spanplus {
          display: none;
          @media screen and (min-width: $desktop-small) {
            display: block;
          }
        }
        .spanplus {
          display: block;
        }
        .spanmoins {
          display: none;
        }
      }
    }
    .mobile__alerte {
      position: relative;
      display: inline-block;
      padding-left: 2px;
      .btn-alerte {
        background: $red;
        color: $white;
        text-align: center;
        border-radius: 4px;
        display: block;
        height: 36px;
        line-height: 36px;
        font-weight: 400;
        padding: 0 15px;
        > span {
          display: none;
          @media screen and (min-width: $tablet-medium) {
            display: inline-block;
          }
        }
        i {
          font-size: 14px;
          @media screen and (min-width: $tablet-medium) {
            margin-left: 10px;
          }
        }
      }
    }
  }
}

.content-search {
  padding-top: 109px;
  @include clearfix;
  @media screen and (min-width: $desktop-small) {
    padding-top: 0;
  }
  &__tabs {
    display: block;
    text-align: right;
    border: 1px solid $color1;
    border-bottom: 0;
    border-radius: 6px 6px 0 0;
    background: $white;
    float: right;
    margin-right: 15px;
    position: relative;
    z-index: 8;
    @media screen and (min-width: $desktop-small) {
      display: none;
    }
  }
  &__tab {
    display: inline-block;
    margin-right: -3px;
    margin-top: -1px;
    margin-left: -1px;
    &:last-child {
      margin-right: -1px;
    }

    &.active {
      a {
        border: 1px solid $color1;
        border-bottom: 0;
        border-radius: 6px 6px 0 0;
        background: $grey;
        color: $textcolor-1;
      }
      &:first-child {
        a {
          &:before {
            content: '';
            display: block;
            position: absolute;
            bottom: -2px;
            left: 0;
            right: 0;
            height: 3px;
            width: 100%;
            background: $grey;
            z-index: 100;
          }
        }
      }
    }
    a {
      display: block;
      padding: 15px;
      border-radius: 6px 6px 0 0;
      position: relative;
      color: $color5;
      font-size: 18px;
    }
  }
  &__main {
    clear: both;
    @include clearfix;
    @media screen and (min-width: $desktop-small) {
      @include flex;
    }
    .contenttab {
      display: none;
      //padding-left: 15px;
      //padding-right: 15px;
      @media screen and (min-width: $desktop-small) {
        display: block;
        padding-left: 0;
        padding-right: 0;
      }
      &:last-child {
        display: block;
      }
    }
  }
  &__left {
    width: 100%;
    position: relative;
    @media screen and (min-width: $desktop-small) {
      width: calc(100% - 670px);
      float: left;
    }
    @media screen and (min-width: $desktop-medium) {
      width: 58%;
    }
    .map {
      position: relative;
      height: 500px;
      @include transition (all .5s ease);
      @media screen and (min-width: $desktop-small) {
        width: 50%;
        position: fixed;
        bottom: 0;
        left: 0;
        height: calc(100% - 106px);
      }
      @media screen and (min-width: $desktop-medium) {
        width: 58%;
      }
      img {
        width: auto;
      }
      > img {
        //width: auto;
        //height: 100%;
        max-width: none;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: auto;
        height: 100%;
      }
      #map {
        width: 100%;
        height: 100%;
      }
      #map-68 {
        width: 100%;
        height: 100%;
        //width: 1000px;
        //height: 1000px;
      }
      .progress-68 {
        position: absolute;
        z-index: 10;
        height: 5px;
        width: 0;
        left: 0;
        right: 0;
        top: 0;
        background: $red;
        @include opacity(1);
        @include transition (all 1s ease);
        &.hide {
          width: 100%;
          @include opacity(0);
          @include transition (all 1s ease);
        }
      }
      .title-map-marker-68 {
        background: $colorpink;
        font-size: 12px;
        font-weight: 600;
        padding: 3px 5px;
        color: $white;
        border-radius: 3px;
        position: relative;
        @include transition (all 0.3s ease);
        &.focus {
          background: $color7;
        }
        &.vendu{
          background: #e8e8e8;
          color: $textcolor-1;
          border: 1px solid #868686;
        }
      }
      .gm-style{
        .gmnoprint{
          img{
            left: 50% !important;
          }
        }
        .gm-style-iw{
          a{
            display: block;
            img{
              width: 200px;
              height: auto;
            }
          }
          h3{
            font-weight: bold;
            padding-top: 10px;
          }
        }
      }
    }
  }
  &__right {
    width: 100%;
    position: relative;
    background: $grey;
    @media screen and (min-width: $desktop-small) {
      float: right;
      width: 670px;
      max-width: 900px;
    }
    @media screen and (min-width: $desktop-medium) {
      width: 670px;
    }
    .search-title {
      padding: 20px 30px;
      background: $grey;
      border-top: 1px solid $color1;
      @include clearfix;

      @media screen and (min-width: $desktop-small) {
        margin-top: 56px;
      }
      &__left {

        @include clearfix;
        @media screen and (min-width: $tablet-medium){
          float: left;
          width: calc(100% - 350px);
          padding-right: 10px;
        }
        .title {
          font-weight: 700;
          font-size: 18px;
          line-height: 18px;
          margin-bottom: 5px;
        }
        .sum-result {
          margin-bottom: 10px;
        }

      }
      &__right {
        @include clearfix;
        @media screen and (min-width: $tablet-medium){
          float: left;
          width: 350px;
        }
        .highlight-label{
          float: left;
          width: calc(100% - 115px);
          @include clearfix;
          .checkbox{
            float: left;
            line-height: 15px;
            margin-right: 15px;
          }
        }
        > .SumoSelect {
          float: left;
          width: 115px;
          > .CaptionCont {
            border: 0;
            background: $grey;
            padding-left: 0;
            padding-right: 0;
            border: 1px solid #ddd;
            >span{
              max-width: 115px;
              padding-right: 15px;
              padding-left:10px;
              line-height: 14px;
              text-overflow: initial;
              word-break: break-word;
              white-space: normal;
              padding-top: 5px;
              label{
                &>i{
                  height: 18px;
                }
              }
            }
          }
          >.optWrapper{
            >.options {
              li {
                &.opt{
                  padding: 6px 0;
                }
                label{
                  max-width: 115px;
                  line-height: 18px;
                  text-overflow: initial;
                  word-break: break-word;
                  white-space: normal;
                }
              }
            }
          }
        }
        .dd-container {
          width: 100% !important;
          .dd-select {
            width: 100% !important;
            background: $grey !important;
            border: 0;
            border-radius: 0;
            height: 46px;
            line-height: 26px;
          }
        }
        .dd-selected {
          font-weight: 500;
          color: $textcolor-1;
          text-align: center;
          padding-right: 25px;
        }
        .dd-options {
          width: 100% !important;
        }
      }
      .search-map_filter-tags{
        clear: both;
        padding-top: 15px;
        > span{
          border: 1px solid $green;
          padding: 5px 10px;
          display: inline-block;
          margin-right: 5px;
          border-radius: 4px;
          margin-bottom: 5px;
          color: $green;
          font-weight: bold;
          .remove-filter{
            background: url('../images/icon-close-green.png') no-repeat center center;
            width: 8px;
            height: 8px;
            display: inline-block;
            background-size: contain;
            margin-left: 5px;
            cursor: pointer;
          }
        }
        &_remove-all{
          span{
            display: inline-block;
            background: $colorpink;
            color: $white;
            padding: 5px 10px;
            border-radius: 4px;
            cursor: pointer;
            @include transition (all .3s ease);
            @include hover {
              background: $colorpink2;
            }
          }
        }
      }
    }
    .list-search {
      background: $grey;
      min-height: 700px;
      //height: calc(100% - 87px);
      padding-left: 15px;
      padding-right: 15px;
      @media screen and (min-width: $desktop-small) {
        padding-left: 0;
        padding-right: 0;
      }

      &__item {
        background: $white;
        margin-bottom: 10px;
        border: 1px solid $color1;
        @include clearfix;
        @include transition (all 0.3s ease);
        @media screen and (min-width: $tablet-medium) {
          @include flex;
        }
        @media screen and (min-width: $desktop-small) {
          border: 0;
          border-top: 1px solid $color1;
          border-bottom: 1px solid $color1;
        }
        &:last-child {
          margin-bottom: 0;
        }
        @include hover {
          background: $color3;
          .list-search__item__left {
            .image {
              img {
                @include scale(1.1);
              }
            }
          }
          .list-search__item__right {
            .item-bottom__right {
              .btn-detail {
                background: $color7;
                color: $white;
              }
            }
          }
        }
        &__left {
          position: relative;
          @media screen and (min-width: $tablet-medium) {
            width: 48%;
            float: left;
          }
          &:before {
            content: '';
            position: absolute;
            z-index: 2;
            bottom: 0;
            left: 0;
            right: 0;
            top: 50%;
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 100%);
          }
          @include hover {
            .image {
              img {
                @include scale(1.1);
              }
            }
          }
          .link-over {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 4;
          }
          .all-label{
            position: absolute;
            left: 10px;
            top: 10px;
            text-transform: uppercase;
            color: $white;
            font-size: 10px;
            font-weight: 500;
            z-index: 3;
          }
          .prix-baisse {
            background: #39c678;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
          }
          .nouveau {
            background: #0dabd0;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
          }
          .urgent{
            background: #c82021;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
          }
          .vendu{
            background: #e8e8e8;
            padding: 5px;
            color: $textcolor-1;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
          }
          .coup-de-coeur{
            background: $colorpink;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
          }
          .image {
            position: relative;
            z-index: 1;
            overflow: hidden;
            img {
              width: 100%;
              @include transition (all .3s ease);
              @include scale(1);
            }
          }
          .content {
            position: absolute;
            z-index: 3;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 15px;
            color: $white;
            line-height: 18px;
            .price {
              font-weight: 300;
              font-style: italic;
              font-size: 14px;
              span {
                font-size: 18px;
                font-weight: 700;
              }
            }
            .short-des {
              font-size: 12px;
              font-weight: 500;
            }
          }
        }
        &__right {
          padding: 20px 15px;
          position: relative;
          @media screen and (min-width: $tablet-medium) {
            width: 52%;
            float: left;
          }
          @media screen and (min-width: $desktop-small) {
            padding: 10px 15px;
          }
          @media screen and (min-width: 1100px) {
            padding: 20px 15px;
          }
          @media screen and (min-width: 1450px) {
            padding: 20px 30px;
          }
          .item-top {
            margin-bottom: 15px;
            @include clearfix;
            .image {
              float: left;
              width: 48px;
              img {
                border-radius: 50%;
                border: 1px solid rgba($textcolor-1, 0.2);
              }
            }
            .des-right {
              float: left;
              width: calc(100% - 48px);
              padding-left: 15px;
              line-height: 18px;
              .publie {
                font-size: 13px;
                font-weight: 500;
                span {
                  text-transform: uppercase;
                  color: $green;
                  font-weight: 600;
                }
              }
              .name {
                color: $blue;
                font-size: 15px;
                font-weight: 500;
              }
              .price-vote {
                font-size: 15px;
                font-weight: 600;
                @include clearfix;
                .price {
                  float: left;
                }
                .vote {
                  float: left;
                  padding-top: 4px;
                  padding-left: 10px;
                  @include clearfix;
                  .star {
                    float: left;
                    margin-right: 1px;
                    color: $yellow;
                    font-size: 11px;
                    line-height: 12px;
                  }
                }
              }
            }
          }
          .item-middle {
            margin-bottom: 20px;
            @include clearfix;
            .three-col {
              text-align: center;
              border-left: 1px solid #e5e6e4;
              width: 33.3%;
              float: left;
              &:first-child {
                border-left: 0;
              }
              .title {
                font-size: 18px;
                margin-bottom: 7px;
                line-height: 18px;
              }
              .text {
                font-size: 13px;
                color: $light-grey;
                line-height: 18px;
              }
            }
          }
          .item-bottom {
            padding-top: 10px;
            border-top: 1px solid $color1;
            @include clearfix;
            @include flex;
            @media screen and (min-width: $tablet-medium) {
              padding-top: 0;
              border-top: 0;
              position: absolute;
              left: 15px;
              right: 15px;
              bottom: 25px;
            }
            @media screen and (min-width: $desktop-small) {
              bottom: 5px;
            }
            @media screen and (min-width: 1100px) {
              left: 15px;
              right: 15px;
              bottom: 25px;
            }
            @media screen and (min-width: $desktop-medium) {
              left: 30px;
              right: 30px;
            }
            &__left {
              float: left;
              width: calc(100% - 90px);
              padding-top: 2px;
              position: relative;
              .inner {
                position: relative;
                top: 50%;
                @include transform(translateY(-50%));
              }
              .depuis {
                font-size: 12px;
                font-weight: 600;
                color: #8c95ae;
                display: inline-block;
              }
              .jours {
                display: inline-block;
                font-size: 12px;
                font-weight: 600;
              }
            }
            &__right {
              float: left;
              width: 90px;
              .btn {
                padding: 9px 10px;
                display: block;
                width: 100%;
                border-radius: 4px;
                text-align: center;
                @include transition (all .3s ease);
                &-detail {
                  background: $color7;
                  color: $white;
                  @media screen and (min-width: $tablet-medium) {
                    background: $color1;
                    color: $textcolor-1;
                  }
                  @include hover {
                    background: $color7;
                    color: $white;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}


#alert-form-wrapper-68{
  width:100%;
  height:100%;
  display:none;
  position:fixed;
  top:0px;
  left:0px;
  background:rgba(0,0,0,0.5);
  z-index: 99;

  #alert-form-68 {
    display: block;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    padding: 30px;
    background: $color3;
    border-radius: 4px;
    overflow-y: auto;
    height: 100%;
    @media screen and (min-width: $tablet-medium){
      width: 500px;
      max-height: 400px;
      height: auto;
    }
    .form-alert-close-68{
      background: url('../images/i-close.png') no-repeat;
      width: 36px;
      height: 37px;
      position: absolute;
      top: 0;
      right: 0;
      text-indent: -999px;
      padding: 10px;
      background-position: center center;
      cursor: pointer;
    }

    .form-wrapper{
      @include clearfix;
    }
    .form-item{
      margin-bottom: 15px;
      padding-left: 15px;
      padding-right: 15px;
      position: relative;
      @include clearfix;
      @media screen and (min-width: $tablet-medium){
        width: 50%;
        float: left;
      }
      > label{
        width: 100%;
        margin-bottom: 5px;
        display: block;
        font-weight: bold;
        //@media screen and (min-width: $tablet-medium){
        //  width: 200px;
        //  float: left;
        //}
      }
      .form-text, .form-email, .form-tel{
        border: 1px solid $color1;
        padding: 8px 10px;
        border-radius: 4px;
        background: $white;
        font-weight: 500;
        font-size: 14px;
        width: 100%;
        resize: none;
        height: 36px;
        @include placeholder {
          font-style: italic;
          color: #909195;
        }
        //@media screen and (min-width: $tablet-medium){
        //  width: calc(100% - 200px);
        //  float: left;
        //}
      }
      .form-select{
        border: 1px solid $color1;
        padding: 10px 20px;
        border-radius: 4px;
        background: $white;
        font-weight: 500;
        font-size: 14px;
        width: 100%;
        resize: none;
        //@media screen and (min-width: $tablet-medium){
        //  width: calc(100% - 200px);
        //  float: left;
        //}
      }
      .SumoSelect{
        //@media screen and (min-width: $tablet-medium){
        //  width: calc(100% - 200px);
        //  float: left;
        //}
      }
    }
    .container-budget-68{
      clear: both;
      .form-no-label{
        width: 100%;
        margin-bottom: 0;
        font-weight: bold;
      }
      .form-item{
        @include clearfix;
        > label{
          width: 50px;
          float: left;
          line-height: 36px;
        }
        > input[type="text"]{
          padding-right: 50px;
          width: calc(100% - 50px);
          float: left;
        }
        .field-suffix{
          position: absolute;
          right: 30px;
          top: 10px;
        }
      }
    }
    .container-surface-68{
      clear: both;
      .form-no-label{
        width: 100%;
        margin-bottom: 0;
        font-weight: bold;
      }
      .form-item{
        @include clearfix;
        > label{
          width: 50px;
          float: left;
          line-height: 36px;
        }
        > input[type="text"]{
          padding-right: 50px;
          width: calc(100% - 50px);
          float: left;
        }
        .field-suffix{
          position: absolute;
          right: 30px;
          top: 10px;
        }
      }
    }
    .form-actions{
      padding-top: 20px;
      clear: both;
      .form-submit{
        padding: 9px 30px;
        display: block;
        width: auto;
        margin: 0 auto;
        border-radius: 4px;
        text-align: center;
        text-transform: uppercase;
        @include transition (all .3s ease);
        background: $colorpink;
        color: $white;
        @include hover {
          background: $darkpink;
          color: $white;
        }
      }
    }
    .container-info-68{
      width: 100%;
      .form-no-label{
        border-bottom: 1px solid $color1;
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 10px;
        padding-bottom: 5px;
        width: 100%;
        text-align: center;
        .alert-filter-tags{
          padding-top: 15px;
          padding-bottom: 15px;
          .item-tag{
            border: 1px solid $green;
            padding: 5px 10px;
            display: inline-block;
            margin-right: 5px;
            border-radius: 4px;
            margin-bottom: 5px;
            color: $green;
            font-weight: bold;
            font-size: 12px;
          }
        }
      }
    }
    .form-alert-68{
      form{
        .container-info-68{
          & + .form-wrapper{
            .form-item{
              width: 100%;
              float: none;
            }
          }
        }

        .form-actions{
          padding-left: 15px;
          padding-right: 15px;
          padding-top: 0;
          .form-submit{
            width: 100%;
          }
        }
      }
    }
  }
}