#webform-submission-rating-form-add-form{
  padding: 100px 0;
  max-width: 600px;
  margin: 0 auto;
  .form-item{
    @include clearfix;
    > label{
      width: 100%;
      margin-bottom: 5px;
      display: block;
      font-weight: bold;
      @media screen and (min-width: $tablet-medium){
        width: 200px;
        float: left;
      }
    }
    .form-text, .form-number, .form-email, .form-tel{
      border: 1px solid $color1;
      padding: 8px 10px;
      border-radius: 4px;
      background: $white;
      font-weight: 500;
      font-size: 14px;
      width: 100%;
      resize: none;
      height: 36px;
      @include placeholder {
        font-style: italic;
        color: #909195;
      }
      @media screen and (min-width: $tablet-medium){
        width: calc(100% - 200px);
        float: left;
      }
    }
    .form-textarea-wrapper{
      @media screen and (min-width: $tablet-medium){
        width: calc(100% - 200px);
        float: left;
      }
      .form-textarea{
        border: 1px solid $color1;
        padding: 8px 10px;
        border-radius: 4px;
        background: $white;
        font-weight: 500;
        font-size: 14px;
        width: 100%;
        resize: none;
        height: 200px;
      }
    }
    .SumoSelect{
      @media screen and (min-width: $tablet-medium){
        width: calc(100% - 200px);
        float: left;
      }
    }
  }
  #edit-star-widget{
    .form-item{
      fieldset{
        label{
          &:before{
            font-size: 20px;
          }
        }
      }
    }
  }
  .captcha{
    text-align: center;
    padding-top: 30px;

    @media screen and (min-width: $tablet-medium){
      padding-left: 200px;
    }
  }
  .form-actions{
    text-align: center;
    padding-top: 30px;
    .form-submit{
      padding: 9px 30px;
      display: block;
      width: auto;
      margin: 0 auto;
      border-radius: 4px;
      text-align: center;
      text-transform: uppercase;
      @include transition (all .3s ease);
      background: $colorpink;
      color: $white;
      @include hover {
        background: $darkpink;
        color: $white;
      }
    }
  }
}
