.StarRating{
  display: flex;
  .starFullSvg{
    display: inline;
    margin-right: 3px;
    .SvgIcon{
      width: 13px;
      height: 13px;
      fill: $yellow;
    }
  }
  .starHalfSvg{
    display: inline;
    margin-right: 3px;
    .SvgIcon{
      width: 13px;
      height: 13px;
      .Right{
        fill: #e2e2e2;
      }
      .Left {
        fill: $yellow;
      }
    }
  }
  .starEmptySvg{
    display: inline;
    margin-right: 3px;
    .SvgIcon{
      width: 13px;
      height: 13px;
      fill: #e2e2e2;
    }
  }
}

