.content-recherche-agent {
  padding-top: 15px;
  @include clearfix;
  @media screen and (min-width: $desktop-small) {
    padding-top: 0;
  }
  &__tabs {
    display: block;
    text-align: right;
    border: 1px solid $color1;
    border-bottom: 0;
    border-radius: 6px 6px 0 0;
    background: $white;
    float: right;
    margin-right: 15px;
    position: relative;
    z-index: 8;
    @media screen and (min-width: $desktop-small) {
      display: none;
    }
  }
  &__tab {
    display: inline-block;
    margin-right: -3px;
    margin-top: -1px;
    margin-left: -1px;
    &:last-child {
      margin-right: -1px;
    }

    &.active {
      a {
        border: 1px solid $color1;
        border-bottom: 0;
        border-radius: 6px 6px 0 0;
        background: $grey;
      }
      &:first-child {
        a {
          &:before {
            content: '';
            display: block;
            position: absolute;
            bottom: -2px;
            left: 0;
            right: 0;
            height: 3px;
            width: 100%;
            background: $grey;
            z-index: 100;
          }
        }
      }
    }
    a {
      display: block;
      padding: 15px;
      border-radius: 6px 6px 0 0;
      position: relative;
    }
  }
  &__main {
    clear: both;
    @include clearfix;
    @media screen and (min-width: $desktop-small) {
      @include flex;
    }
    .contenttab {
      display: none;
      //padding-left: 15px;
      //padding-right: 15px;
      @media screen and (min-width: $desktop-small) {
        display: block;
        padding-left: 0;
        padding-right: 0;
      }
      &:last-child {
        display: block;
      }
    }
  }
  &__left {
    width: 100%;
    position: relative;
    @media screen and (min-width: $desktop-small) {
      width: 50%;
      float: left;
    }
    @media screen and (min-width: $desktop-medium) {
      width: 58%;
    }
    .map {
      position: relative;
      @include transition (all .5s ease);
      @media screen and (min-width: $desktop-small) {
        width: 50%;
        position: fixed;
        bottom: 0;
        left: 0;
        height: calc(100% - 50px);
      }
      @media screen and (min-width: $desktop-medium) {
        width: 58%;
      }

      > img {
        width: auto;
        max-width: none;
        height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
      }
      #map {
        width: 100%;
        height: 100%;
      }
    }
  }
  &__right {
    width: 100%;
    position: relative;
    background: $grey;
    @media screen and (min-width: $desktop-small) {
      float: right;
      width: 50%;
      max-width: 900px;
    }
    @media screen and (min-width: $desktop-medium) {
      width: 670px;
    }
    .search-title {
      padding: 25px 30px 30px;
      background: #f8f8f8;
      border-top: 1px solid $color1;
      margin-bottom: 10px;
      border-bottom: 1px solid $color1;
      @include clearfix;
      &__top {
        @include clearfix;
        .title {
          font-weight: 700;
          font-size: 28px;
          line-height: 26px;
          margin-bottom: 10px;
          text-align: center;
        }
        .sum-result {
          font-size: 16px;
          color: $light-grey;
          text-align: center;
          margin-bottom: 20px;
        }
      }
      &__bottom {
        .input-search-agent {
          width: 100%;
          border-radius: 6px;
          position: relative;
          @include clearfix;
          @include box-shadow(0, 0, 15px, rgba($textcolor-1, 0.1));
          @media screen and (min-width: $tablet-medium) {
            width: calc(100% - 110px);
            float: left;
          }
          .input {
            padding: 10px;
            height: 46px;
            color: #929292;
            font-size: 14px;
            background: $white;
            width: calc(100% - 48px);
            border-radius: 6px 0 0 6px;
            @include placeholder {
              font-style: italic;
              color: #929292;
            }
          }
          .button-search {
            height: 46px;
            width: 48px;
            border-radius: 0 6px 6px 0;
            display: block;
            float: right;
            background: $green;
            color: $white;
            font-size: 18px;
            @include transition (all .3s ease);
            @include hover {
              background: $blackgreen;
            }
          }
          .bs-autocomplete-menu {
            top: 50px;
            padding: 10px;
            border-radius: 0;
            box-shadow: none;
            li {
              padding: 5px 0;
            }
          }
        }
        .sum-result {
          text-align: right;
          position: relative;
          height: 46px;
          @media screen and (min-width: $tablet-medium) {
            width: 100px;
            float: left;
          }
          .text {
            position: absolute;
            right: 0;
            top: 50%;
            @include transform(translateY(-50%));
            span {
              font-weight: bold;
            }
          }
        }
      }
    }
    .list-search {
      background: $grey;
      //height: calc(100% - 87px);
      min-height: 450px;
      padding-left: 15px;
      padding-right: 15px;
      @media screen and (min-width: $desktop-small) {
        padding-left: 0;
        padding-right: 0;
      }

      &__item {
        background: $white;
        margin-bottom: 10px;
        border: 1px solid $color1;
        @include transition (all .3s ease);
        @include clearfix;
        @media screen and (min-width: $tablet-medium) {
          @include flex;
        }
        @media screen and (min-width: $desktop-small) {
          border: 0;
          border-top: 1px solid $color1;
          border-bottom: 1px solid $color1;
        }

        &:last-child {
          margin-bottom: 0;
          @media screen and (min-width: $tablet-medium) {
            border-bottom: 0;
          }
        }
        &:first-child {
          border-bottom: 1px solid $color1;
        }
        @include hover {
          background: $color3;
          .list-search__item__left {
            .image {
              img {
                @include scale(1.1);
              }
            }
          }
          .list-search__item__right {
            .item-top {
              .btn-detail {
                background: $colorpink;
                color: $white;
              }
            }
          }
        }
        &__left {
          position: relative;
          @media screen and (min-width: $tablet-medium) {
            width: 180px;
            float: left;
          }
          @include hover {
            .image {
              img {
                @include scale(1.1);
              }
            }
          }
          .link-over {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 4;
          }
          .image {
            position: relative;
            z-index: 1;
            overflow: hidden;
            img {
              width: 100%;
              @include transition (all .3s ease);
              @include scale(1);
            }
          }
        }
        &__right {
          @media screen and (min-width: $tablet-medium) {
            width: calc(100% - 180px);
            float: left;
            border-left: 1px solid $color1;
            @include flex;
          }
          .item-top {
            padding: 13px 15px;
            @include clearfix;
            @media screen and (min-width: $tablet-medium) {
              width: 50%;
            }
            @media screen and (min-width: $desktop-medium) {
              padding: 13px 30px;
              width: 44%;
            }
            .description {
              @media screen and (min-width: $tablet-medium) { 
                width: 100%;
              }
              .name {
                color: $color4;
                font-size: 20px;
                font-weight: 500;
                margin-bottom: 3px;
              }
              .agent {
                color: $green;
                font-size: 13px;
                text-transform: uppercase;
                margin-bottom: 5px;
              }
              .tel {
                font-size: 14px;
                font-weight: 600;
              }
            }
            .group-btn {
              padding-bottom: 15px;
              padding-top: 15px;
              @media screen and (min-width: $tablet-medium) {
                float: left;
                width: 150px;
                padding-top: 20px;
                padding-bottom: 0;
              }
              .btn {
                padding: 9px 10px;
                display: block;
                width: 100%;
                border-radius: 4px;
                text-align: center;
                @include transition (all .3s ease);
                &-detail {
                  background: $colorpink;
                  color: $white;
                  @media screen and (min-width: $tablet-medium) {
                    background: $color1;
                    color: $textcolor-1;
                  }
                  @include hover {
                    background: $darkpink;
                    color: $white;
                  }
                }
              }
            }
          }
          .item-bottom {
            padding-top: 10px;
            text-align: center;
            @include clearfix;
            @include flex;
            @media screen and (min-width: $tablet-medium) {
              padding-top: 0;
              border-top: 0;
              width: 50%;
              flex-direction: column;
              border-left: 1px solid #e5e6e4;
            }
            @media screen and (min-width: $desktop-medium){
              width: 56%;
            }
            &__left {
              float: left;
              width: 50%;
              position: relative;
              border-top: 1px solid #e5e6e4;
              padding: 13px 15px;
              font-size: 15px;
              @media screen and (min-width: $tablet-medium) {
                width: 100%;
              }
              span {
                font-weight: 600;
                font-size: 15px;
              }
              &:first-child{
                border-top: 1px solid #e5e6e4;
                @media screen and (min-width: $tablet-medium) {
                  border-top:none;
                }
              }
              &:nth-child(2n+1) {
                border-right: 1px solid #e5e6e4;
                @media screen and (min-width: $tablet-medium) {
                  border-right: none;
                }
              }
            }
            .price-vote {
              font-size: 15px;
              font-weight: 600;
              @include clearfix;
              .price {
                display: inline-block;
              }
              .vote {
                display: inline-block;
                padding-top: 4px;
                padding-left: 10px;
                @include clearfix;
                .star {
                  float: left;
                  margin-right: 1px;
                  color: $yellow;
                  font-size: 11px;
                  line-height: 12px;
                }
              }
            }
            .txt-hono {
              color: #0084bf;
              font-weight: 500;
              font-size: .9375rem;
            }
          }
        }
      }
    }
  }
}