.user-login-form {
  max-width: 500px;
  margin: 0 auto;
  padding: 100px 15px;
  @media screen and (min-width: $tablet-medium){
    padding: 100px 0;
  }
  .form-item {
    margin-bottom: 30px;
  }
  label {
    margin-bottom: 10px;
    display: block;
    font-size: 16px;
  }
  .form-text {
    border: 1px solid $color1;
    padding: 10px 20px;
    border-radius: 4px;
    background: $white;
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    resize: none;
    margin-bottom: 5px;
    @include placeholder {
      font-style: italic;
      color: #909195;
    }
  }
  .description {
    font-style: italic;
  }
  .form-actions {
    text-align: center;
    @media screen and (min-width: $tablet-medium){
      text-align: left;
    }
    .button {
      background: $colorpink;
      color: $white;
      font-weight: 600;
      text-transform: uppercase;
      padding: 15px 70px;
      border-radius: 4px;
      @include transition (all .3s ease);
      @include hover {
        background: $darkpink;
      }
    }
  }
}

.vendor-properties-list{
  padding-top: 100px;
  line-height: 30px;
  font-weight: bold;
  max-width: 600px;
  margin: 0 auto;
  a{
    @include hover{
      color: $red;
    }
  }
}
.profile{
  padding-bottom: 100px;
  max-width: 600px;
  margin: 0 auto;
  .form-item{
    .label{}
  }
  .field--name-field-vendor-of-agent{
    .field__label{
      margin-bottom: 15px;
    }
    .field__item{
      a{
        padding: 15px 30px;
        display: inline-block;
        width: auto;
        border-radius: 4px;
        text-align: center;
        @include transition (all .3s ease);
        background: $color7;
        color: $white;
        @include hover {
          background: $color8;
          color: $white;
        }
      }
    }
  }
}