.title-detail {
    padding-top: 20px;
    padding-bottom: 10px;
    background: $color3;
    position: relative;
    transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    @include transition (all .3s ease);
    @media screen and (min-width: $desktop-small) {
        border-bottom: 1px solid $color1;
        padding-bottom: 0;
        position: fixed;
        left: 0;
        right: 0;
        top: 50px;
        z-index: 8;
    }

    &.fixed {
        position: fixed;
        left: 0;
        right: 0;
        top: 50px;
        z-index: 8;
    }
    .row {
        @media screen and (min-width: $desktop-small) {
            @include flex;
        }
    }
    .main-title {
        padding-right: 0;
        position: relative;

        @media screen and (min-width: $tablet-medium) {
            padding-right: 130px;
            margin-bottom: 16px;
        }
        .title {
            font-size: 26px;
            font-weight: 600;
            margin: 0 0 15px;
            padding-right: 50px;
            p {
                display: inline;
            }
            @media screen and (min-width: $tablet-medium) {
                padding-right: 0;
            }
        }
        .titlecenter {
            font-size: 28px;
            font-weight: 600;
            margin: 0 0 15px;
            padding: 20px 0;
            text-align: center;
            @media screen and (min-width: $tablet-medium) {
                padding-right: 0;
            }
        }
        .des {
            font-size: 15px;
            ul {
                padding: 0;
                margin: 0;
                li {
                    display: inline-block;
                    padding: 0 15px 0 13px;
                    border-right: 1px solid $color1;
                    margin-bottom: 15px;
                    @media screen and (min-width: $tablet-medium) {
                        margin-bottom: 0;
                    }
                    &:first-child {
                        padding-left: 0;
                    }
                    &:last-child {
                        border-right: 0;
                        padding: 0;
                    }
                    &:nth-last-child(2) {
                        border-right: 0;
                    }
                }
            }
        }
        .all-label {
            text-transform: uppercase;
            color: #fff;
            font-size: .625rem;
            font-weight: 500;
            z-index: 3;
        }
        .prix-baisse {
            background: #39c678;
            padding: 5px;
            display: inline-block;
            margin-right: 5px;
            border-radius: 2px;
        }
        .nouveau {
            background: #0dabd0;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
        }
        .urgent {
            background: #c82021;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
        }
        .vendu {
            background: #e8e8e8;
            padding: 5px;
            color: $textcolor-1;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
        }
        .coup-de-coeur {
            background: $colorpink;
            padding: 5px;
            border-radius: 2px;
            display: inline-block;
            margin-right: 5px;
        }
        .btn-partager {
            position: absolute;
            top: 0;
            right: 0;
            background: $white;
            height: 36px;
            line-height: 32px;
            padding: 0 8px;
            display: inline-block;
            vertical-align: middle;
            border-radius: 4px;
            border: 1px solid $color1;
            @include transition (all .3s ease);
            @media screen and (min-width: $tablet-medium) {
                padding: 0 15px;
            }
            i {
                font-size: 16px;
                position: relative;
                @media screen and (min-width: $tablet-medium) {
                    margin-left: 5px;
                }
            }
            span {
                display: none;
                @media screen and (min-width: $tablet-medium) {
                    display: inline-block;
                }
            }
            @include hover {
                background: $light-grey;
                color: $white;
            }
        }

    }

    .main-tabs {
        display: none;
        @include clearfix;
        @media screen and (min-width: $desktop-small) {
            display: block;
        }
        ul {
            padding: 0;
            margin: 0;
            display: table;
            width: 100%;
            @include clearfix;
            li {
                display: table-cell;
                list-style-type: none;
                text-align: center;
                @media screen and (min-width: $desktop-medium) {
                    //width: 20%;
                }
                &:first-child {
                    a {
                        border-radius: 4px 0 0;
                    }
                }
                &:last-child {
                    a {
                        border-radius: 0 4px 0 0;
                        border-right: 1px solid $color1;
                    }
                }
                a {
                    border: 1px solid $color1;
                    border-bottom: 0;
                    border-right: 0;
                    line-height: 36px;
                    display: block;
                    background: $grey;
                    //padding-right: 15px;
                    //padding-left: 15px;
                    @include transition (all .3s ease);
                    @include hover {
                        background: $light-grey;
                        color: $white;
                    }
                    &.btn-back {
                        background: $light-grey;
                        color: $white;
                        vertical-align: middle;
                        line-height: 35px;
                        i {
                            margin-right: 5px;
                            font-size: 11px;
                            top: -2px;
                            position: relative;
                        }
                    }
                    &.active {
                        background: $colorpink;
                        color: $white;
                    }
                }
            }
        }
    }
    .prices {
        @media screen and (min-width: $desktop-small) {
            position: absolute;
            left: 0;
            right: 0;
            top: 10%;
            @include transform(translateY(-10%));
        }
    }
}

.content-detail-annonce {
    position: relative;
    @media screen and (min-width: $desktop-small) {
        padding-top: 139px;
    }
    .row {
        position: relative;
    }
    //.side-left {
    //min-height: 2000px;
    //}
    .side-right {
        position: relative;
        //background: $white;
        display: none;
        //@media screen and (min-width: 1101px) {
        //z-index: 8;
        //}
        @media screen and (min-width: $desktop-small) {
            display: block;
        }
        //.inner-sticky {
        //will-change: min-height;
        //padding-top: 30px;
        //}
        .inner {
            //padding-top: 30px;
            padding-bottom: 30px;
            position: relative;
            margin-top: -38px;
            z-index: 100;
            //transform: translate(0, 0);
            //transform: translate3d(0, 0, 0);
            //will-change: position, transform;
            &.fixed {
                position: fixed;
                width: 350px;
                top: 190px;
                z-index: 10;
            }
        }
    }
    .content-vendeur {
        font-size: 17px;
        line-height: 26px;
        font-weight: 400;
        padding-bottom: 40px;
        &:first-of-type {
            padding-top: 30px;
        }
        .title {
            color: $green;
            text-transform: uppercase;
            font-size: 20px;
            font-weight: 600;
            padding-bottom: 15px;
            border-bottom: 1px solid $color1;
            margin: 0 0 20px;
        }
        .detail-votre-bien {
            .subtitle {
                font-size: 26px;
                font-weight: 600;
                margin: 0 0 15px;
                padding-right: 50px;
                @media screen and (min-width: $tablet-medium) {
                    padding-right: 0;
                }
            }
            .des {
                font-size: 15px;
                margin-bottom: 20px;
                ul {
                    padding: 0;
                    margin: 0;
                    li {
                        display: inline-block;
                        padding: 0 15px 0 13px;
                        border-right: 1px solid $color1;
                        margin-bottom: 15px;
                        @media screen and (min-width: $tablet-medium) {
                            margin-bottom: 0;
                        }
                        &:first-child {
                            padding-left: 0;
                        }
                        &:last-child {
                            border-right: 0;
                            padding: 0;
                        }
                        &:nth-last-child(2) {
                            border-right: 0;
                        }
                    }
                }
            }
            .prix-baisse {
                background: #5d5d5d;
                padding: 5px;
                text-transform: uppercase;
                color: #fff;
                font-size: .625rem;
                font-weight: 500;
                z-index: 3;
                border-radius: 2px;
                line-height: 11px;
            }
        }
        .group-btn-votre-bien {
            display: block;
            margin-bottom: 15px;
            .btn-votre-bien {
                background: $colorpink;
                color: $white;
                font-weight: 400;
                font-size: 14px;
                padding: 5px 30px;
                width: auto;
                border-radius: 4px;
                display: inline-block;
                @include transition (all .3s ease);
                @include hover {
                    background: $darkpink;
                }
            }
        }

        p {
            margin-bottom: 20px;
        }
        .smalltitle {
            font-size: 19px;
            font-weight: 600;
            margin-bottom: 15px;
        }
        .diffuse {
            .groupicon {
                @include clearfix;
                .itemicon {
                    border: 1px solid $color1;
                    border-left: 0;
                    width: 120px;
                    height: 120px;
                    float: left;
                    text-align: center;
                    line-height: 120px;
                    position: relative;
                    &:first-child {
                        border-left: 1px solid $color1;
                    }
                    img {
                        margin: 0 auto;
                        position: absolute;
                        left: 0;
                        right: 0;
                        top: 50%;
                        @include transform(translateY(-50%));
                    }
                }
            }
        }

        .table {
            &-list {
                padding-top: 10px;
            }
            &-item {
                margin-bottom: 10px;
                @include clearfix;
                @include flex;
                .dates {
                    width: 101px;
                    float: left;
                    border: 1px solid $color1;
                    border-radius: 4px 0 0 4px;
                    background: $grey;
                    line-height: 40px;
                    text-align: center;
                    font-size: 14px;
                    font-weight: 500;
                    @media screen and (min-width: $tablet-medium) {
                        height: 42px;
                    }
                }
                .table-item-right {
                    width: calc(100% - 101px);
                    float: left;
                    border: 1px solid $color1;
                    border-left: 0;
                    border-radius: 0 4px 4px 0;
                    background: $grey;
                    font-size: 14px;
                    line-height: 40px;
                    padding-left: 20px;
                    @include clearfix;
                    @media screen and (min-width: $tablet-medium) {
                        height: 42px;
                    }
                    &-txt {
                        text-transform: uppercase;
                        margin-right: 15px;
                        display: inline-block;
                    }
                    &-price {
                        font-size: 18px;
                        font-weight: 600;
                        margin-right: 15px;
                        display: inline-block;
                    }
                    &-old-price {
                        font-size: 14px;
                        text-decoration: line-through;
                        display: inline-block;
                    }
                    .namefile {
                        font-size: 14px;
                        font-weight: 500;
                        display: inline-block;
                    }
                    .group-icon {
                        padding-right: 20px;
                        @include clearfix;
                        @media screen and (min-width: $tablet-medium) {
                            float: right;
                            padding-right: 0;
                        }
                        .eye {
                            display: block;
                            float: left;
                            width: 33.3%;
                            height: 40px;
                            text-align: center;
                            @media screen and (min-width: $tablet-medium) {
                                border-left: 1px solid $color1;
                                width: 42px;
                            }
                            .icon-eye {
                                background: url('../images/icon-eye.png') no-repeat;
                                width: 18px;
                                height: 14px;
                                display: block;
                                margin: 12px auto 0;
                            }
                        }
                        .down {
                            border-left: 1px solid $color1;
                            display: block;
                            float: left;
                            width: 33.3%;
                            height: 40px;
                            text-align: center;
                            @media screen and (min-width: $tablet-medium) {
                                width: 42px;
                            }
                            .icon-down {
                                background: url('../images/icon-down.png') no-repeat;
                                width: 12px;
                                height: 14px;
                                display: block;
                                margin: 12px auto 0;
                            }
                        }
                        .trash {
                            border-left: 1px solid $color1;
                            display: block;
                            float: left;
                            width: 33.3%;
                            height: 40px;
                            text-align: center;
                            @media screen and (min-width: $tablet-medium) {
                                width: 42px;
                            }
                            .icon-trash {
                                background: url('../images/icon-trash.png') no-repeat;
                                width: 10px;
                                height: 14px;
                                display: block;
                                margin: 12px auto 0;
                            }
                        }
                    }
                }
            }
        }
        .block-visite {
            &-item {
                border-bottom: 1px solid $color1;
                margin-bottom: 20px;
            }
            &-head {
                margin-bottom: 15px;
                @include clearfix;
                &-left {
                    @media screen and (min-width: $tablet-medium) {
                        float: left;
                        width: 50%;
                    }
                }
                &-right {
                    @media screen and (min-width: $tablet-medium) {
                        float: left;
                        width: 50%;
                        text-align: right;
                    }
                }
                .titlevisite {
                    font-size: 17px;
                    font-weight: 600;
                    display: inline-block;
                    margin-right: 15px;
                    vertical-align: middle;
                }
                .note {
                    display: inline-block;
                    background: #5d5d5d;
                    padding: 5px 10px;
                    text-transform: uppercase;
                    color: $white;
                    font-size: 10px;
                    font-weight: 500;
                    z-index: 3;
                    border-radius: 3px;
                    margin-right: 10px;
                    line-height: 11px;
                    vertical-align: middle;
                }
                .suivi {
                    font-size: 17px;
                    display: inline-block;
                    position: relative;
                    padding-right: 20px;
                    &:after {
                        content: '';
                        background: $color1;
                        height: 21px;
                        width: 1px;
                        display: none;
                        position: absolute;
                        right: 0;
                        top: 5px;
                        @media screen and (min-width: $tablet-medium) {
                            display: block;
                        }
                    }
                    &.colorgreen {
                        .prix {
                            color: $green;
                        }
                    }
                    &.colorred {
                        .prix {
                            color: $color10;
                        }
                    }
                    &.coloryellow {
                        .prix {
                            color: $yellow;
                        }
                    }
                    .euro {
                        font-size: 30px;
                        color: $colorpink;
                        font-weight: 600;
                        position: relative;
                        bottom: -5px;
                        padding-left: 5px;
                    }
                    .icon {
                        display: inline-block;
                        vertical-align: sub;
                        padding-left: 10px;
                        padding-right: 5px;
                    }
                    .prix {
                        font-size: 16px;
                        color: $colorpink;
                        font-weight: 600;
                    }
                }
                .avis {
                    font-size: 17px;
                    display: inline-block;

                    @media screen and (min-width: $tablet-medium) {
                        padding-left: 15px;
                    }
                    .vote {
                        display: inline-block;
                        padding-top: 4px;
                        padding-left: 10px;
                        @include clearfix;
                        .star {
                            float: left;
                            margin-right: 1px;
                            color: $yellow;
                            font-size: 11px;
                            line-height: 12px;
                        }
                    }
                }

            }
        }
        &.dossier-vente {
            .group-btn-votre-bien {
                margin-top: 20px;
            }
        }
    }
    .description {
        font-size: 17px;
        line-height: 26px;
        font-weight: 400;
        padding-bottom: 40px;
        .title {
            color: $green;
            text-transform: uppercase;
            font-size: 20px;
            font-weight: 600;
            padding-bottom: 15px;
            border-bottom: 1px solid $color1;
            margin: 0 0 20px;
        }
        strong {
