        }
        .texte {
            overflow: hidden;

            p {
                margin-bottom: 20px;
            }
        }
        .plus, .less {
            color: $color4;
            font-style: italic;
            font-weight: 500;
            display: block;
            margin-top: 15px;
            i {
                margin-left: 10px;
                font-size: 13px;
            }
        }
    }
    .explorer {
        padding-bottom: 40px;
