                            background: #c9a6d0;
                            width: 40%;
                            &:after {
                                border-left: 10px solid #c9a6d0;
                            }
                        }
                    }
                    &:nth-child(3) {
                        div {
                            background: #b797c5;
                            width: 45%;
                            &:after {
                                border-left: 10px solid #b797c5;
                            }
                        }
                    }
                    &:nth-child(4) {
                        div {
                            background: #93639d;
                            width: 50%;
                            &:after {
                                border-left: 10px solid #93639d;
                            }
                        }
                    }
                    &:nth-child(5) {
                        div {
