    z-index: 100;
    background: $color3;
    border-bottom: 1px solid $color1;
    border-top: 1px solid $color1;
    text-align: center;
    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
        padding: 10px 30px;
    }
    @media screen and (min-width: $desktop-small) {
        display: none;
    }
    .btn-demander {
        background: $colorpink;
        color: $white;
        font-weight: 400;
        text-align: center;
        display: inline-block;
        padding: 10px 5px;
        border-radius: 4px;
        width: 49%;
        float: left;
    }
    .btn-faire {
        background: $color7;
        color: $white;
        font-weight: 400;
        text-align: center;
        display: inline-block;
        padding: 10px 5px;
        border-radius: 4px;
        width: 49%;
        float: right;
    }
}

.agence {
    margin-bottom: 20px;
    &-title {
        color: $green;
        background: $grey;
        border-radius: 4px 4px 0 0;
        border: 1px solid $color1;
        text-align: center;
        font-size: 19px;
        font-weight: 500;
        padding: 16px 10px;
    }
    &-content {
        background: $color3;
        border-radius: 0 0 4px 4px;
        border: 1px solid $color1;
        border-top: 0;
        text-align: center;
        padding: 30px 10px;

        .des {
            font-size: 14px;
            text-transform: uppercase;
            font-weight: bold;
        }
        .price {
            font-weight: 600;
            font-size: 30px;
        }
    }
}

.content-vendeur {
    .side-right {
        z-index: 10;
        .inner {
            padding-top: 0;
            margin-top: -31px;
        }
    }
}

.page-vendeur {
    .title-detail {
        .main-title {
            padding-right: 0;
            @media screen and (min-width: $desktop-small) {
                padding-right: 130px;
            }
        }
    }
}