.content-envoi {
  position: relative;
  margin-top: -17px;
  padding-bottom: 50px;
  @media screen and (min-width: $desktop-small) {
    padding-bottom: 0;
  }
  textarea.form-control {
    margin-top: 30px;
  }
}

.envoi-row {
  @include clearfix;
  @media screen and (min-width: $desktop-small) {
    margin-right: -15px;
    margin-left: -15px;
  }
  &-left {
    @media screen and (min-width: $desktop-small) {
      width: calc(100% - 350px);
      float: left;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  &-right {
    @media screen and (min-width: $desktop-small) {
      width: 350px;
      float: left;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

.approximatif {
  margin-left: -30px;
  margin-right: -30px;
  background: $color3;
  padding: 15px 30px;
  border-top: 1px solid $color1;
  border-bottom: 1px solid $color1;
}

.comissions {
  border: 1px solid $color1;
  border-radius: 4px;
  padding: 20px;
  text-align: center;
  .highlight {
    color: $colorpink;
    font-weight: bold;
  }
}