.page-detail-actu {
  .wrap-back {
    padding-bottom: 65px;
    padding-top: 30px;
    position: relative;
    top: 0;
    @include transition (all .3s ease);
    &.nofixed {
      padding-bottom: 0;
    }
    .btn-back {
      border-radius: 6px;
    }
  }
}

.content-detail-actu {
  position: relative;
  margin-top: -45px;
  padding-bottom: 20px;
  //padding-top: 86px;

  .social {
    text-align: center;
    margin-bottom: 25px;
    padding-top: 20px;
    border-top: 1px solid $color1;
    @media screen and (min-width: $tablet-medium) {
      padding-top: 0;
      border-top: 0;
    }

    ul {
      margin: 0;
      padding: 0;
      li {
        list-style-type: none;
        display: inline-block;
        margin-right: 20px;
        @media screen and (min-width: $tablet-medium) {
          margin-right: 12px;
        }
        &:last-child {
          margin-right: 0;
        }
        a {
          background: #e9e9ea;
          border-radius: 50%;
          height: 38px;
          width: 38px;
          line-height: 38px;
          text-align: center;
          color: #020202;
          display: block;
          @include transition (all .3s ease);
          @media screen and (min-width: $tablet-medium) {
            height: 38px;
            width: 38px;
            line-height: 38px;
          }
          @include hover {
            background: $green;
            color: $white;
          }
        }
      }
    }
    .sharethis-wrapper {
      > span {
        display: inline-block;
        > span.stButton {
          margin-right: 6px;
          margin-left: 6px;
          .stLarge {
            background-image: none !important;
            background: #e9e9ea;
            border-radius: 50%;
            height: 38px;
            width: 38px;
            line-height: 38px;
            text-align: center;
            color: #020202;
            display: block;
            @include transition (all .3s ease);
            @media screen and (min-width: $tablet-medium) {
              height: 38px;
              width: 38px;
              line-height: 38px;
            }
            @include hover {
              background: $green;
              color: $white;
            }
            &:before {
              font-family: 'fontello';
              display: block;
              color: #020202;
            }
          }
        }
        &.st_facebook_large {
          .stButton {
            .stLarge {
              &:before {
                content: '\e80b';
              }
            }
          }
        }
        &.st_twitter_large {
          .stButton {
            .stLarge {
              &:before {
                content: '\e80f';
              }
            }
          }
        }
        &.st_linkedin_large {
          .stButton {
            .stLarge {
              &:before {
                content: '\e80e';
              }
            }
          }
        }
        &.st_pinterest_large {
          .stButton {
            .stLarge {
              &:before {
                content: '\e80d';
              }
            }
          }
        }
      }
    }
  }
  .banner {
    position: relative;
    z-index: 12;
    margin-bottom: 30px;
    img {
      display: block;
      width: 100%;
      border: 1px solid #e5e6e4;
    }
  }
  .description {
    .content-note {
      margin-bottom: 25px;
      @include clearfix;
      .note {
        display: inline-block;
        background: #5d5d5d;
        padding: 5px 10px;
        text-transform: uppercase;
        color: $white;
        font-size: 10px;
        font-weight: 500;
        z-index: 3;
        border-radius: 2px;
        margin-right: 10px;
      }
      .daytime {
        color: #8c95ae;
        font-size: 12px;
        display: inline-block;
        span {
          color: #5d5d5d;
          font-weight: 600;
        }
      }
    }
    .short-des {
      font-size: 22px;
      line-height: 28px;
      margin-bottom: 20px;
    }
    .title {
      font-size: 34px;
      line-height: 36px;
      font-weight: 600;
      margin: 0 0 25px;
    }
    h3 {
      color: $green;
      font-size: 20px;
      font-weight: 600;
      line-height: 20px;
      margin-bottom: 15px;
    }
    p {
      font-size: 17px;
      line-height: 26px;
      font-weight: 400;
      margin-bottom: 30px;
      strong {
        font-weight: 500;
      }
    }
    .iconface {
      display: inline-block;
    }
  }
  .page-cms-68{
    padding: 100px 0;
  }
  .relate-post {
    &-title {
      font-size: 19px;
      font-weight: 600;
      line-height: 26px;
      text-align: center;
      padding-bottom: 20px;
      padding-top: 20px;
      border-top: 1px solid $color1;
      border-bottom: 1px solid $color1;
      margin-bottom: 30px;
    }
    &-item {
      padding-bottom: 20px;
      border-bottom: 1px solid $color1;
      margin-bottom: 20px;
      .image {
        margin-bottom: 15px;
        img {
          width: 100%;
          display: block;
        }
      }
      .content {
        &-top {
          margin-bottom: 15px;
          @include clearfix;
          .note {
            display: inline-block;
            background: #5d5d5d;
            padding: 5px 10px;
            text-transform: uppercase;
            color: $white;
            font-size: 10px;
            font-weight: 500;
            z-index: 3;
            border-radius: 2px;
            margin-right: 10px;
          }
          .daytime {
            color: #8c95ae;
            font-size: 12px;
            display: inline-block;
            span {
              color: #5d5d5d;
              font-weight: 600;
            }
          }
        }
        .title {
          font-size: 18px;
          font-weight: 600;
          line-height: 22px;
          margin: 0;
        }
      }
    }
  }
}
.no_banner{
  .wrap-back{
    padding-bottom: 30px;
  }
  .content-detail-actu{
    margin-top: 30px;
  }
}