body.page-home {
    background: $white;
    //overflow-x: hidden;
    overflow: hidden;
    height: auto;
    &:after {
        content: '';
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        background: rgba($textcolor-1, 0.7);
        display: none;
        @include opacity(0);
        @include transition (all 2s ease);
        @media screen and (min-width: $desktop-small) {
            display: none;
        }
    }
    .content-home__left {
        .inner {
            &:before {
                content: '';
                width: 100%;
                height: 100%;
                background-image: linear-gradient(180deg,rgba(26, 26, 26, 0.8),hsla(0, 0%, 42%, 0.2));
                position: absolute;
                top: 0;
                left: 0;
                z-index: 2;
            }
        }
    }
    &.add-bottom {
        &:after {
            z-index: 5;
            @include opacity(1);
        }
        .footer {
            bottom: 0;
        }
        .content-home__left {
            .inner {
                position: absolute;
                bottom: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    .footer {
        position: relative;
        @include transition (all 2s ease);
        //@media screen and (min-width: $desktop-small){
        //  position: fixed;
        //  left: 0;
        //  right: 0;
        //  bottom: -700px;
        //  z-index: 20;
        //}
        &-logos {
            display: block;
        }
    }
}

.content-home {
    position: relative;
    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
        @include flex;
    }
    &__left {
        position: relative;
        width: 100%;
        @media screen and (min-width: $desktop-small) {
            float: left;
            width: 50%;
        }
        .inner {
            //@include transition (all 2s ease);
            position: relative;
            height: 100%;
            @media screen and (min-width: $desktop-small) {
                width: 50%;
                position: fixed;
                bottom: 0;
                left: 0;
                height: 100%;
            }
        }
        &__top {
            position: relative;
            //overflow: hidden;
            height: 100%;
            width: 100%;
            .search { 
                position: absolute;
                left: 50%!important;
                transform: translate(-50%, -50%);
                right: 0;
                z-index: 20;
                /*padding: 0 30px;*/
                @media screen and (min-width: $tablet-small){
                    min-width: 460px;
                }
                @media screen and (max-width: $tablet-small - 1px){
                    top: 370px !important;
                    width: 100%;
                    max-width: 300px;
                }
                /*@media screen and (min-width: $tablet-medium){
                    min-width: 520px;
                    width: auto;
                }*/
                //margin: 0 15px;
                //@media screen and (min-width: $desktop-medium){
                //  margin: 0;
                //}

            }
        }
        &__bottom {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 20;
            text-align: center;
            margin: 0 auto;
            display: none;
            @media screen and (min-width: $desktop-small) {
                display: block;
            }
            ul {
                padding: 0;
                margin: 0;
                li {
                    display: inline-block;
                    padding: 20px 5px;
                    a {
                        @include transition (all .3s ease);
                        @include hover {
                            color: $white;
                        }
                    }
                }
            }
        }
        .image {
            position: relative;
            z-index: 1;
            min-height: 440px;
            overflow: hidden;
            @media screen and (min-width: $tablet-medium) {
                height: 100%;
            }
            img {
                width: auto;
                min-width: 170%;
                min-height: 100%;
                height: auto;
                margin: auto;
                position: absolute;
                top: -100%;
                left: -98%;
                right: -100%;
                bottom: -100%;
                max-width: none;
                max-height: 100%;
                //@media screen and (min-width: $desktop-small){
                //  width: auto;
                //  min-width: 100%;
                //  min-height: 100%;
                //  height: auto;
                //  margin: auto;
                //  position: absolute;
                //  top: -100%;
                //  left: -100%;
                //  right: -100%;
                //  bottom: -100%;
                //  max-width: none;
                //}
            }
        }
        .description {
            position: absolute;
            //top: 50%;
            left: 0;
            top: calc(50% - 140px);
            right: 0;
            z-index: 20;
            @include transform(translateY(-50%));
            @media screen and (min-width: $tablet-medium) {
                top: calc(50% - 120px);
            }
            .bg-cover {
                background: url('../images/bg-cover.png') no-repeat center;
                min-height: 185px;
                padding-top: 15px;
                margin-bottom: 20px;
                position: relative;
                top: 105px;
                @media screen and (min-width: $tablet-small) {
                    top: 50px;
                }
                @media screen and (min-width: $tablet-medium) {
                    padding-top: 20px;
                    margin-bottom: 33px;
                }
            }
            .title {
                font-weight: 700;
                font-size: 28px;
                line-height: 30px;
                margin: 0 auto 10px;
                text-align: center;
                color: $white;
                width: 200px;
                @media screen and (min-width: $tablet-medium) {
                    font-weight: 800;
                    font-size: 37px;
                    line-height: 40px;
                    width: auto;
                    margin: 0 auto 20px;
                }
            }
            .sub-title {
                color: $white;
                font-size: 15px;
                font-weight: 500;
                text-align: center;
                @media screen and (min-width: $tablet-small){
                    font-size: 18px;
                }
                //@media screen and (min-width: $tablet-medium) {
                //
                //}
            }
            .group-btn {
                text-align: center;
                margin-bottom: 0;
                padding-bottom: 20px;
                position: relative;
                top: 95px;
                @include clearfix;
                @media screen and (min-width: $tablet-small) {
                    top: 50px;
                }
                @media screen and (min-width: $tablet-medium) {
                    margin-bottom: 0;
                    padding-bottom: 0;
                }
                @media screen and (min-width: $desktop-small) {
                    margin-bottom: 0;
                    padding-bottom: 0;
                }
                .btn {
                    padding: 18px 5px;
                    text-align: center;
                    font-size: 18px;
                    line-height: 20px;
                    color: $white;
                    float: left;
                    width: 50%;
                    @media screen and (min-width: $tablet-medium) {
                        margin: 0 10px;
                        display: inline-block;
                        width: auto;
                        float: none;
                        padding: 18px 45px;
                        @include border-radius(5px);
                    }
                    span {
                        font-weight: 800;
                        display: block;
                    }
                    &-red {
                        background: $red;
                        @include hover {
                            background: $blackred;
                        }
                    }
                    &-green {
                        background: $green;
                        @include hover {
                            background: $blackgreen;
                        }
                    }
                }
            }
            .search {
                margin: 0 15px;
                @media screen and (min-width: $desktop-medium) {
                    margin: 0 auto;
                }
            }
        }
    }
    &__right {
        padding: 30px 0;
        position: relative;
        @media screen and (min-width: $tablet-medium) {
            padding: 30px;
        }
        @media screen and (min-width: $desktop-small) {
            float: right;
            width: 50%;
        }
        .lists {
            width: calc(100% + 70%);
            left: -35%;
            @include clearfix;
            @media screen and (min-width: $tablet-medium) {
                margin-left: -5px;
                margin-right: -5px;
                width: 100%;
                left: 0;
            }
            &__item {
                margin-bottom: 20px;
                @media screen and (min-width: $tablet-medium) {
                    float: left;
                    width: 50%;
                    padding-right: 5px;
                    padding-left: 5px;
                }
            }
            .inner {
                position: relative;
            }
            .link-over {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: 4;
            }
            .item-top {
                margin-bottom: 15px;
                @include clearfix;
                .image {
                    float: left;
                    width: 48px;
                    img {
                        border-radius: 50%;
                        border: 1px solid rgba($textcolor-1, 0.2);
                    }
                }
                .des-right {
                    float: left;
                    width: calc(100% - 48px);
                    padding-left: 15px;
                    line-height: 18px;
                    .publie {
                        font-size: 13px;
                        font-weight: 500;
                        span {
                            text-transform: uppercase;
                            color: $green;
                            font-weight: 600;
                        }
                    }
                    .name {
                        color: $blue;
                        font-size: 15px;
                        font-weight: 500;
                    }
                    .price-vote {
                        font-size: 15px;
                        font-weight: 600;
                        @include clearfix;
                        .price {
                            float: left;
                        }
                        .vote {
                            float: left;
                            padding-top: 4px;
                            padding-left: 10px;
                            @include clearfix;
                            .star {
                                float: left;
                                margin-right: 1px;
                                color: $yellow;
                                font-size: 11px;
                                line-height: 12px;
                            }
                        }
                    }
                }
            }
            .item-bottom {
                position: relative;
                &:before {
                    content: '';
                    position: absolute;
                    z-index: 2;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    top: 50%;
                    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
                }
                @include hover {
                    .image {
                        img {
                            @include scale(1.1);
                        }
                    }
                }
                .image {
                    position: relative;
                    z-index: 1;
                    overflow: hidden;
                    @media screen and (max-width: $tablet-medium) {
                        height: 200px;
                    }
                    img {
                        position: relative;
                        width: 100%;
                        @include transition (all .3s ease);
                        @include scale(1);
                        @media screen and (max-width: $tablet-medium) {
                            left: 50%;
                            width: auto;
                            max-width: none;
                            height: 100%;
                            transform: translateX(-50%);
                        }
                    }
                }
                .content {
                    position: absolute;
                    z-index: 3;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    padding: 15px;
                    color: $white;
                    line-height: 18px;
                    .price {
                        font-weight: 300;
                        font-style: italic;
                        font-size: 14px;
                        span {
                            font-size: 18px;
                            font-weight: 700;
                        }
                    }
                    .short-des {
                        font-size: 12px;
                        font-weight: 500;

