            }
            .map-home {
                position: relative;
                margin-top: 10px;
                @media screen and (min-width: $tablet-medium) {
                    margin-top: 0;
                }
                img {
                    width: 100%;
                }
            }
        }
    }
}

.search {
    height: 105px;
    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
        height: 46px;
    }
    @media screen and (min-width: $desktop-medium) {
        width: 580px;
    }
    &__select {
        width: 100%;
        margin-bottom: 13px;
        @media screen and (min-width: $tablet-medium) {
            width: 110px;
            float: left;
            margin-bottom: 0;
        }
        .dd-container {
            width: 100% !important;
            .dd-select {
                width: 100% !important;
                background: $white !important;
                border: 0;
                border-radius: 4px;
                border-right: 1px solid #dddddd;
                height: 46px;
                line-height: 26px;
                @media screen and (min-width: $tablet-medium) {
                    border-radius: 4px 0 0 4px;
                }
            }
        }
        .dd-selected {
            font-weight: 500;
            color: $green;
            text-align: left;
            padding-right: 25px;
            @media screen and (min-width: $tablet-medium) {
                text-align: center;
            }
        }
        .dd-pointer {
            top: 33%;
            @include transition (all .3s ease);
            &.dd-pointer-down {
                display: block;
                border: 0;
                font-size: 11px;
                &:before {
                    content: '\e802';
                    position: absolute;
                    top: 50%;
                    right: 0;
                    font-family: 'fontello';
                }
                &.dd-pointer-up {
                    display: block !important;
                    border: 0 !important;
                    font-size: 11px;
                    top: 42%;
                    &:before {
                        content: '\e805';
                        position: absolute;
                        top: 50%;
                        right: 0;
                        font-family: 'fontello';
                    }
                }
            }

        }
        .dd-options {
            width: 100% !important;
        }
    }
    &__input {
        width: 100%;
        @include clearfix;
        @media screen and (min-width: $tablet-medium) {
            width: calc(100% - 110px);
            float: left;
        }
        .input {
            padding: 10px;
            height: 46px;
            color: #929292;
            font-size: 14px;
            background: $white;
            width: calc(100% - 48px);
            border-radius: 4px 0 0 4px;
            @include placeholder {
                font-style: italic;
            }
            @media screen and (min-width: $tablet-medium) {
                border-radius: 0;
            }
        }
        .button-search {
            height: 46px;
            width: 48px;
            border-radius: 0 4px 4px 0;
            display: block;
            float: right;
            background: $green;
            color: $white;
            font-size: 18px;
            @include transition (all .3s ease);
            @include hover {
                background: $blackgreen;
            }
        }
    }
}

.search-on-map {
    height: 46px;
    margin: 0 auto;
    position: relative;
    left: .9375rem;
    right: .9375rem;
    top: -1.4375rem;

    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
        //top: 30px;
    }
    @media screen and (min-width: $desktop-medium) {
        width: 464px;
        left: 0;
        right: 0;
    }

    &__input {
        width: 100%;
        float: left;
        @include clearfix;
        @include box-shadow(0, 0, 20px, rgba($textcolor-1, 0.2));
        .input {
            padding: 10px;
            height: 46px;
            color: #929292;
            font-size: 14px;
            background: $white;
            width: calc(100% - 48px);
            border-radius: 4px 0 0 4px;
            @include placeholder {
                font-style: italic;
            }
        }
        .button-search {
            height: 46px;
            width: 48px;
            border-radius: 0 4px 4px 0;
            display: block;
            float: right;
            background: $green;
            color: $white;
            font-size: 18px;
            @include transition (all .3s ease);
            @include hover {
                background: $blackgreen;
            }
        }
    }
}

.path {
    stroke-dasharray: 1000;
    stroke-dashoffset: 0;
    animation: dash 5s linear;
}

@keyframes dash {
    from {
        stroke-dashoffset: 1000;
    }
    to {
        stroke-dashoffset: 0;
    }
}

.sr .content-home__right .list-blocks .inner .dash,
.sr .content-home__right .list-blocks .inner .picto,
.sr .content-home__right .list-blocks .inner .title,
.sr .content-home__right .list-blocks .inner .subtext {
    visibility: hidden;
}

#test {
    display: none;
