.content-detail {
  padding-top: 25px;
  position: relative;
  @media screen and (min-width: $desktop-small) {
    padding-top: 99px;
  }
  &:before {
    content: '';
    background: $color3;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 142px;
    display: block;
    @media screen and (min-width: $tablet-medium) {
      display: none;
    }
  }

  &:after {
    content: '';
    background: $color1;
    position: absolute;
    height: 1px;
    left: 0;
    right: 0;
    top: 142px;
    display: block;
    z-index: 1;
    @media screen and (min-width: $tablet-medium) {
      display: none;
    }
  }
  .side-right {
    .social {
      display: none;
      @media screen and (min-width: $tablet-medium) {
        display: block;
      }
    }
  }
  .social {
    text-align: center;
    margin-bottom: 20px;
    padding-top: 20px;
    border-top: 1px solid $color1;
    @media screen and (min-width: $tablet-medium) {
      padding-top: 0;
      border-top: 0;
    }
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style-type: none;
        display: inline-block;
        margin-right: 20px;
        @media screen and (min-width: $tablet-medium) {
          margin-right: 12px;
        }
        &:last-child {
          margin-right: 0;
        }
        a {
          background: #e9e9ea;
          border-radius: 50%;
          height: 38px;
          width: 38px;
          line-height: 38px;
          text-align: center;
          color: #020202;
          display: block;
          @include transition (all .3s ease);
          @media screen and (min-width: $tablet-medium) {
            height: 38px;
            width: 38px;
            line-height: 38px;
          }
          @include hover {
            background: $green;
            color: $white;
          }
        }
      }
    }
  }
  .biens {
    margin-bottom: 20px;
    padding-top: 30px;
    border-top: 1px solid $color1;
    @media screen and (min-width: $tablet-medium) {
      padding-top: 0;
      border-top: 0;
    }
    .btn-biens {
      display: block;
      border: 1px solid $color1;
      border-radius: 4px;
      padding: 14px 30px 15px;
      text-align: center;
      font-size: 17px;
      font-weight: 600;
      background: $color3;
      @include transition (all .3s ease);
      //@include hover{
      //  background: $green;
      //  color: $white;
      //}
    }
  }
  & + .equipe {
    display: none;
    @media screen and (min-width: $tablet-medium) {
      display: block;
    }
  }
}

.detail-agent {
  margin-bottom: 20px;
  position: relative;
  z-index: 2;
  @include clearfix;
  .image {
    width: 181px;
    height: 181px;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 15px;
    border: 1px solid rgba($textcolor-1, 0.2);
    @media screen and (min-width: $tablet-medium) {
      width: 235px;
      height: auto;
      float: left;
      border-radius: 0;
      border: 0;
      margin-bottom: 0;
    }
    img {
      display: block;
      margin: 0 auto;
      width: 100%;
      @media screen and (min-width: $tablet-medium) {
        width: 100%;
      }
    }
  }
  .description {
    @media screen and (min-width: $tablet-medium) {
      width: calc(100% - 235px);
      padding-left: 30px;
      float: left;
    }
    &-nom {
      margin-bottom: 25px;
      @include clearfix;
      @media screen and (min-width: $tablet-medium) {
        margin-bottom: 10px;
      }
      .nom {
        font-size: 25px;
        font-weight: 400;
        margin: 0 0 8px;
        padding: 0;
        text-align: center;
        @media screen and (min-width: $tablet-medium) {
          width: 60%;
          float: left;
          text-align: left;
          margin: 0;
        }
      }
      .agence {
        line-height: 20px;
        text-align: center;
        @media screen and (min-width: $tablet-medium) {
          width: 40%;
          float: left;
          text-align: right;
        }
        .partenaire {
          font-weight: bold;
          color: $green;
          text-transform: uppercase;
        }
        &-nom {
          font-weight: 400;
        }
      }
    }
    &-detail {
      @include clearfix;
      @include flex;
      .detail-block {
        padding: 14px 15px;
        border: 1px solid $color1;
        border-left: 0;
        display: block;
        font-size: 15px;
        font-weight: 400;
        span {
          font-weight: 600;
        }
      }
      .detail-vote {
        width: 50%;
        float: left;
        font-size: 15px;
        font-weight: 600;
        padding-left: 0;
        text-align: center;
        border-bottom: 0;
        @include clearfix;
        @media screen and (min-width: $tablet-medium) {
          width: 27.5%;
          border-bottom: 1px solid $color1;
        }
        @media screen and (min-width: $desktop-small) {
          width: 50%;
          border-bottom: 0;
        }
        @media screen and (min-width: 1100px) {
          width: 27.5%;
          border-bottom: 1px solid $color1;
        }
        .nb-vote {
          display: inline-block;
        }
        .vote {
          display: inline-block;
          padding-top: 4px;
          padding-left: 10px;
          font-weight: 600;
          @include clearfix;
          .star {
            float: left;
            margin-right: 1px;
            color: $yellow;
            font-size: 11px;
            line-height: 12px;
          }
        }
      }
      .avis {
        width: 50%;
        float: left;
        text-align: center;
        padding: 14px 10px;
        border-right: 0;
        border-bottom: 0;
        @media screen and (min-width: $tablet-medium) {
          width: 17.5%;
          border-right: 1px solid $color1;
          border-bottom: 1px solid $color1;
        }
        @media screen and (min-width: $desktop-small) {
          width: 50%;
          border-right: 0;
          border-bottom: 0;
        }
        @media screen and (min-width: 1100px) {
          width: 17.5%;
          border-right: 1px solid $color1;
          border-bottom: 1px solid $color1;
        }
      }
      .transactions {
        width: 50%;
        float: left;
        text-align: center;
        @media screen and (min-width: $tablet-medium) {
          width: 27.5%;
        }
        @media screen and (min-width: $desktop-small) {
          width: 50%;
        }
        @media screen and (min-width: 1100px) {
          width: 27.5%;
        }
      }
      .honoraires {
        width: 50%;
        float: left;
        color: $color4;
        font-weight: 500;
        border-right: 0;
        text-align: center;
        @media screen and (min-width: $tablet-medium) {
          width: 27.5%;
          text-align: left;
        }
        @media screen and (min-width: $desktop-small) {
          width: 50%;
        }
        @media screen and (min-width: 1100px) {
          width: 27.5%;
        }
      }
    }
    &-infos {
      padding: 10px;
      font-weight: 400;
      text-align: center;
      line-height: 18px;
      @media screen and (min-width: $tablet-medium) {
        text-align: left;
      }
      span {
        font-weight: 600;
        display: block;
        @media screen and (min-width: $tablet-medium) {
          display: inline-block;
          margin-right: 5px;
        }
      }
      i {
        font-size: 18px;
        margin-right: 7px;
        vertical-align: middle;
        display: block;
        @media screen and (min-width: $tablet-medium) {
          display: inline-block;
        }
      }
      .txt {
        display: block;
        vertical-align: middle;
        padding-top: 2px;
        @media screen and (min-width: $tablet-medium) {
          display: inline-block;
        }
      }
      .detail-time {
        padding: 8px 0;
        vertical-align: middle;
        @media screen and (min-width: $tablet-medium) {
          @include displayflex;
        }
      }
      .detail-langue {
        padding: 8px 0;
        vertical-align: middle;
        @media screen and (min-width: $tablet-medium) {
          @include displayflex;
        }
      }
      .detail-secteur {
        padding: 8px 0;
        vertical-align: middle;
        @media screen and (min-width: $tablet-medium) {
          @include displayflex;
        }
      }
    }
    &-bottom {
      border-top: 1px solid $color1;
      padding: 20px 0;
      @include clearfix;
      &__left {
        text-align: center;
        margin-bottom: 15px;
        @media screen and (min-width: $tablet-medium) {
          float: left;
          width: calc(100% - 180px);
          text-align: left;
          margin-bottom: 0;
          padding-left: 10px;
        }
        @media screen and (min-width: $desktop-small) {
          width: calc(100% - 140px);
        }
        @media screen and (min-width: 1100px) {
          width: calc(100% - 180px);
        }
      }
      &_right {
        text-align: center;
        @media screen and (min-width: $tablet-medium) {
          float: left;
          width: 180px;
          text-align: right;
        }
        @media screen and (min-width: $desktop-small) {
          width: 140px;
        }
        @media screen and (min-width: 1100px) {
          width: 180px;
        }
      }
      .number-tel {
        font-weight: 600;
        font-size: 18px;
        @include transition (all .3s ease);
        @include hover {
          color: $colorpink;
        }
        i {
          color: $red;
          font-size: 18px;
          margin-right: 10px;
        }
      }
      .btn-poser {
        background: $colorpink;
        color: $white;
        display: inline-block;
        border-radius: 4px;
        padding: 9px 30px;
        text-align: center;
        @include transition (all .3s ease);
        @include hover {
          background: $colorpink2;
        }
        @media screen and (min-width: $desktop-small) {
          padding: 9px 10px;
        }
        @media screen and (min-width: 1100px) {
          padding: 9px 30px;
        }
      }
    }
  }
}

.content-description {
  font-size: 17px;
  font-weight: 400;
  line-height: 26px;
  .title {
    margin: 0 0 20px;
    color: $green;
    text-transform: uppercase;
    padding: 15px 0;
    border-bottom: 1px solid $color1;
    font-size: 20px;
    font-weight: 600;
  }
  p {
    margin-bottom: 15px;
  }
  .video {
    text-align: center;
    margin-bottom: 20px;
    img {
      display: block;
    }
  }
  .form {
    font-size: 14px;
    background: $color3;
    margin-bottom: 25px;
    .inner {
      margin-right: -9px;
      margin-left: -9px;
      padding: 20px;
      @include transition (all .3s ease);
      @include clearfix;
    }
    &__col {
      @media screen and (min-width: $tablet-medium) {
        width: 50%;
        float: left;
        padding-left: 9px;
        padding-right: 9px;
      }
    }
    &__col__full {
      @media screen and (min-width: $tablet-medium) {
        width: 100%;
        float: left;
        padding-left: 9px;
        padding-right: 9px;
      }
    }
    .SumoSelect {

      > .CaptionCont {
        padding-right: 20px;
        padding-left: 20px;
        font-weight: 500;
      }
    }
    .group-btn {
      text-align: left;
      @media screen and (min-width: $tablet-medium) {
        text-align: right;
      }
      .btn-envoyer {
        background: $colorpink;
        color: $white;
        font-weight: 400;
        padding: 5px 65px;
        border-radius: 4px;
        @include transition (all .3s ease);
        @include hover {
          background: $colorpink2;
        }
      }
    }
    .form-actions {
      .btn-envoyer {
        background: $colorpink;
        color: $white;
        font-weight: 400;
        padding: 5px 65px;
        border-radius: 4px;
        @include transition (all .3s ease);
        @include hover {
          background: $colorpink2;
        }
      }
    }
  }
  .group-vote {
    padding-bottom: 30px;
    &__top {
      font-size: 22px;
      font-weight: 600;
      margin-bottom: 15px;
      @include clearfix;
      .vote-title {
        float: left;
      }
      .vote {
        float: left;
        padding-top: 4px;
        padding-left: 15px;
        padding-right: 15px;
        @include clearfix;
        .star {
          float: left;
          margin-right: 1px;
          color: $yellow;
          font-size: 17px;
          line-height: 12px;
        }
      }
      .StarRating{
        .starFullSvg{
          margin-right: 5px;
          .SvgIcon{
            width: 19px;
            height: 19px;
          }
        }
        .starHalfSvg{
          margin-right: 5px;
          .SvgIcon{
            width: 19px;
            height: 19px;
          }
        }
        .starEmptySvg{
          margin-right: 5px;
          .SvgIcon{
            width: 19px;
            height: 19px;
          }
        }
      }
      .avis {
        font-size: 16px;
        color: #909195;
      }
    }
    &__bottom {
      .progress-item {
        font-size: 16px;
        font-weight: 600;
        vertical-align: middle;
        width: 220px;
        @include clearfix;
        &__left {
          float: left;
          width: 30px;
        }
        &__middle {
          float: left;
          width: calc(100% - 70px);
          .progress {
            margin-top: 12px;
          }
        }
        &__right {
          color: $color5;
          float: left;
          width: 40px;
          padding-left: 15px;
        }
      }
    }
  }
  .group-comment {
    border-top: 1px solid $color1;
    .comment-item {
      border-bottom: 1px solid $color1;
      padding-bottom: 20px;
      padding-top: 20px;
      .comment-vote {
        font-size: 15px;
        font-weight: 600;
        margin-bottom: 10px;
        @include clearfix;
        .vote-title {
          float: left;
          line-height: 20px;
        }
        .vote {
          float: left;
          padding-top: 4px;
          padding-left: 15px;
          padding-right: 15px;
          @include clearfix;
          .star {
            float: left;
            margin-right: 1px;
            color: $yellow;
            font-size: 11px;
            line-height: 12px;
          }
        }
      }
      .comment-text {
        font-size: 17px;
        line-height: 26px;
        color: $black;
        font-weight: 400;
        margin-bottom: 7px;
      }
      .comment-name {
        color: $color5;
        font-size: 16px;
        font-weight: 500;
        i {
          font-size: 18px;
          vertical-align: top;
          margin-right: 5px;
        }
      }
    }
    .plus-commentaires {
      padding: 20px 0;

      .link {
        color: $color4;
        font-size: 17px;
        font-weight: 600;
        display: block;
        @include transition (all .3s ease);
        @include hover {
          color: $green;
        }
        i {
          margin-left: 5px;
          font-size: 12px;
        }
      }
    }
  }
}

.equipe {
  padding-top: 30px;
  padding-bottom: 50px;

  &__inner {
    @media screen and (min-width: $desktop-small) {
      padding-left: 100px;
      padding-right: 100px;
    }
  }
  .title {
    margin: 0 0 42px;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
  }
  .group-member {
    text-align: center;
    margin-bottom: 30px;
    @include clearfix;
    .item-member {
      @media screen and (min-width: $tablet-medium) {
        width: 33.3%;
        float: left;
      }
    }
    .avatar {
      text-align: center;
      margin-bottom: 15px;
      img {
        display: block;
        margin: 0 auto;
        border-radius: 50%;
        border: 1px solid rgba($textcolor-1, 0.2);
        width: auto;
      }
    }
    .position {
      color: $green;
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 600;
      line-height: 22px;
      a {
        color: $green;
      }
    }
    .name {
      font-size: 18px;
      font-weight: 600;
      line-height: 22px;
      a {
        color: $textcolor-1;
      }
    }
    &.owl-carousel{
      .item-member{
        width: auto;
        float: none;
      }
    }
    .owl-item {
      cursor: pointer;
    }
    .owl-nav {
      .owl-prev, .owl-next {
        position: absolute;
        text-align: center;
        line-height: 72px;
        background: $white;
        top: 50%;
        @include transform(translateY(-50%));
        font-size: 20px;
        color: $textcolor-1;
        .icon-arrow-left {
          position: relative;
          margin-left: 30px;
        }
        .icon-arrow-right {
          position: relative;
          margin-right: 30px;
        }
      }
      .owl-prev {
        left: -76px;
        @media screen and (min-width: 1200px) {
          left: -116px;
        }
      }
      .owl-next {
        right: -76px;
        @media screen and (min-width: 1200px) {
          right: -116px;
        }
      }
    }
  }
  .description {
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    text-align: center;
    span {
      font-weight: 600;
    }
  }
}

.list-relate {
  margin-bottom: 30px;
  @media screen and (min-width: $tablet-medium) {
    margin-bottom: 0;
  }
  .link-over {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 4;
  }
  .item-relate {
    position: relative;
    @media screen and (min-width: $tablet-medium) {
      margin-bottom: 20px;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      bottom: 0;
      left: 0;
      right: 0;
      top: 50%;
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
    }
    @include hover {
      .image {
        img {
          @include scale(1.1);
        }
      }
    }
    .image {
      position: relative;
      z-index: 1;
      overflow: hidden;
      img {
        width: 100%;
        @include transition (all .3s ease);
        @include scale(1);
      }
    }
    .content {
      position: absolute;
      z-index: 3;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 15px;
      color: $white;
      line-height: 18px;
      .price {
        font-weight: 300;
        font-style: italic;
        font-size: 14px;
        span {
          font-size: 18px;
          font-weight: 700;
        }
      }
      .short-des {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
}