.content-actu {
  padding-bottom: 20px;
  .social {
    text-align: center;
    margin-bottom: 25px;
    padding-top: 20px;
    border-top: 1px solid $color1;
    @media screen and (min-width: $tablet-medium) {
      padding-top: 56px;
      border-top: 0;
    }
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style-type: none;
        display: inline-block;
        margin-right: 20px;
        @media screen and (min-width: $tablet-medium) {
          margin-right: 12px;
        }
        &:last-child {
          margin-right: 0;
        }
        a {
          background: #e9e9ea;
          border-radius: 50%;
          height: 38px;
          width: 38px;
          line-height: 38px;
          text-align: center;
          color: #020202;
          display: block;
          @include transition (all .3s ease);
          @media screen and (min-width: $tablet-medium) {
            height: 38px;
            width: 38px;
            line-height: 38px;
          }
          @include hover {
            background: $green;
            color: $white;
          }
        }
      }
    }
  }

  .par-theme {
    text-align: center;
    padding: 10px 0;
    .SumoSelect {
      width: auto;
      > .CaptionCont {
        border: 0;
      }
    }
  }
  .top-post {
    padding-bottom: 20px;
    border-bottom: 1px solid #e5e6e4;
    margin-bottom: 27px;
    .image {
      margin-bottom: 20px;
      img {
        width: 100%;
        display: block;
      }
    }
    .content {
      &-top {
        margin-bottom: 15px;
        @include clearfix;
        .note {
          display: inline-block;
          background: #5d5d5d;
          padding: 5px 10px;
          text-transform: uppercase;
          color: $white;
          font-size: 10px;
          font-weight: 500;
          z-index: 3;
          border-radius: 2px;
          margin-right: 10px;
        }
        .daytime {
          color: #8c95ae;
          font-size: 12px;
          display: inline-block;
          span {
            color: #5d5d5d;
            font-weight: 600;
          }
        }
      }
      .title {
        font-size: 26px;
        font-weight: 600;
        line-height: 26px;
        margin: 0 0 15px;
      }
      .des {
        font-size: 17px;
        font-weight: 400;
        line-height: 26px;
      }
    }
  }
  .group-btn {
    text-align: center;
    margin-bottom: 30px;
    .btn-plus-articles {
      font-size: 14px;
      background: $color1;
      height: 34px;
      line-height: 34px;
      padding-left: 30px;
      padding-right: 30px;
      display: inline-block;
      border-radius: 6px;
      @include transition (all .3s ease);
      @include hover {
        background: $orange;
        color: $white;
      }
    }
  }
}

.list-posts {
  margin-bottom: 30px;
  @include clearfix;
  @media screen and (min-width: $tablet-medium) {
    margin-right: -11px;
    margin-left: -11px;
  }
  .item-post {
    padding-bottom: 35px;

    @media screen and (min-width: $tablet-medium) {
      width: 50%;
      float: left;
      padding-right: 11px;
      padding-left: 11px;
    }
  }
  .image {
    margin-bottom: 20px;
    img {
      width: 100%;
      display: block;
    }
  }
  .content {
    &-top {
      margin-bottom: 15px;
      @include clearfix;
      .note {
        display: inline-block;
        background: #5d5d5d;
        padding: 5px 10px;
        text-transform: uppercase;
        color: $white;
        font-size: 10px;
        font-weight: 500;
        z-index: 3;
        border-radius: 2px;
        margin-right: 10px;
      }
      .daytime {
        color: #8c95ae;
        font-size: 12px;
        display: inline-block;
        span {
          color: #5d5d5d;
          font-weight: 600;
        }
      }
    }
    .title {
      font-size: 22px;
      font-weight: 600;
      line-height: 26px;
      margin: 0 0 15px;
      @media screen and (min-width: $tablet-medium) {
        height: 80px;
      }
    }
    .des {
      font-size: 17px;
      font-weight: 400;
      line-height: 26px;
      @media screen and (min-width: $tablet-medium) {
        height: 80px;
      }
    }
  }
}

.derniers-posts {

  .title {
    font-size: 19px;
    font-weight: 600;
    line-height: 26px;
    text-align: center;
    padding-bottom: 20px;
    padding-top: 20px;
    border-top: 1px solid $color1;
    border-bottom: 1px solid $color1;
    margin-bottom: 20px;
    span {
      color: #35a1f2;
      display: block;
      font-weight: 400;
      i {
        font-size: 15px;
        margin-left: 5px;
      }
    }
  }
  .posts-sidebar {
    &-item {
      margin-bottom: 20px;
      padding-bottom: 20px;
      border-bottom: 1px solid $color1;
      &-top {
        font-size: 12px;
        color: $color9;
        margin-bottom: 20px;
        @include clearfix;
        .text-left {
          float: left;
        }
        .text-right {
          float: right;
        }
      }
      .subtext {
        margin-bottom: 15px;
        font-size: 15px;
        color: $black;
        a {
          color: $color4;
          font-style: italic;
        }
      }
      .des {
        font-size: 15px;
        color: $black;
        margin-bottom: 15px;
        a {
          color: $color4;
          font-style: italic;
          display: block;
        }
      }
      .image {
        img {
          display: block;
          width: 100%;
        }
      }
    }
  }
}