.footer {
  background: $textcolor-1;
  padding: 0;
  position: relative;
  @media screen and (min-width: $desktop-medium) {
    position: absolute;
    left: 0%;
    right: 0;
    bottom: 0;
  }
  &-logos {
    text-align: center;
    padding-top: 0;
    padding-bottom: 30px;
    background: $white;
    display: none;
    @media screen and (min-width: $tablet-medium) {
      border-top: 1px solid #dde6ee;
      padding-top: 45px;
      padding-bottom: 45px;
    }
    ul {
      margin: 0;
      padding: 0;
      li {
        display: inline-block;
        padding: 15px;
        width: 49%;
        text-align: center;
        @media screen and (min-width: $tablet-medium) {
          padding: 0 40px;
          width: auto;
        }
        a {
          display: block;
          img {
            display: block;
            margin: 0 auto;
          }
        }
      }
    }
  }
  &-top {
    margin-bottom: 38px;
    padding: 30px 15px 0;
    margin-left: -15px;
    margin-right: -15px;
    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
      margin-bottom: 55px;
      padding: 40px 30px 0;
    }
    .logo {
      width: 90px;
      float: left;
      padding-left: 15px;
      padding-right: 15px;
      @media screen and (min-width: $tablet-medium) {
        width: 120px;
        float: left;
      }
      @media screen and (min-width: $tablet-medium2) {
        width: 20%;
        float: left;
      }
      img {
        width: 90px;
        height: auto;
        @media screen and (min-width: $tablet-medium) {
          width: auto;
        }
      }
    }
    .social {
      width: calc(100% - 90px);
      float: left;
      text-align: right;
      padding-left: 15px;
      padding-right: 15px;
      @media screen and (min-width: $tablet-medium) {
        width: calc(100% - 120px);
        float: right;
        text-align: right;
      }
      @media screen and (min-width: $tablet-medium2) {
        width: 270px;
        float: left;
        text-align: left;
      }
      @media screen and (min-width: $desktop-medium) {
        width: 26.6%;
        float: left;
      }
      ul {
        margin: 10px 0 0;
        padding: 0;
        li {
          list-style-type: none;
          display: inline-block;
          margin-right: 5px;
          @media screen and (min-width: $tablet-medium) {
            margin-right: 8px;
          }
          @media screen and (min-width: 1100px) {
            margin-right: 12px;
          }
          &:last-child {
            margin-right: 0;
          }
          a {
            background: #909195;
            border-radius: 50%;
            height: 30px;
            width: 30px;
            line-height: 30px;
            text-align: center;
            color: #020202;
            display: block;
            @include transition (all .3s ease);
            @media screen and (min-width: $tablet-medium) {
              height: 38px;
              width: 38px;
              line-height: 38px;
            }
            @include hover {
              background: $red;
              color: $white;
            }
          }
        }
      }
    }
    .newsletter {
      clear: both;
      padding-top: 20px;
      padding-left: 15px;
      padding-right: 15px;
      @media screen and (min-width: $tablet-medium2) {
        width: calc(100% - 20% - 270px);
        float: right;
        clear: none;
        padding-top: 0;
      }
      @media screen and (min-width: $desktop-medium) {
        width: 53.4%;
        float: right;
      }
      &-form {
        padding-top: 8px;
        @include clearfix;
        .label {
          color: $white;
          font-size: 15px;
          line-height: 34px;
          font-weight: 400;
          margin-bottom: 5px;
          @media screen and (min-width: $tablet-medium) {
            float: left;
            width: 210px;
            margin-bottom: 0;
          }
        }
        .newsletter-input {
          @include clearfix;
          @media screen and (min-width: $tablet-medium) {
            width: calc(100% - 210px);
            float: left;
          }
          .input {
            padding: 10px 20px;
            height: 34px;
            color: #929292;
            font-size: 14px;
            font-weight: 400;
            background: $white;
            border-radius: 4px 0 0 4px;
            width: calc(100% - 100px);
            @include placeholder {
              font-style: italic;
            }
          }
          .button-send {
            height: 34px;
            width: 100px;
            border-radius: 0 4px 4px 0;
            display: block;
            float: right;
            background: $red;
            color: $white;
            font-weight: 400;
            @include transition (all .3s ease);
            @include hover {
              background: $blackred;
            }
          }
        }
      }
    }
  }
  &-middle {
    margin-bottom: 15px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: -15px;
    margin-right: -15px;
    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
      padding-left: 30px;
      padding-right: 30px;
      margin-bottom: 35px;
    }
    .col-bt {
      height: auto;
      overflow: hidden;
      padding-left: 15px;
      padding-right: 15px;
      margin-bottom: 30px;
      @media screen and (min-width: $tablet-medium) {
        margin-bottom: 0;
      }
      &1 {
        @media screen and (min-width: $tablet-medium) {
          float: left;
          width: 20%;
        }
      }
      &2 {
        @media screen and (min-width: $tablet-medium) {
          float: left;
          width: 26.6%;
        }
      }
      &.hideContent {
        ul {
          height: auto;
          overflow: hidden;
        }
      }
      h2 {
        font-weight: 700;
        font-size: 15px;
        color: $white;
        margin: 0 0 10px;
      }
      ul {
        padding: 0;
        margin: 0;
        //height: 170px;
        overflow: hidden;
        @include transition (all .3s ease);
        li {
          list-style-type: none;
          display: list-item;
          font-weight: 400;
          font-size: 15px;
          a {
            color: #909195;
            line-height: 28px;
            display: block;
            @include transition (all .3s ease);
            @include hover {
              color: $red;
            }
          }
        }
      }
      .plus {
        color: #00ba9f;
        background: transparent;
        position: relative;
        &:after {
          content: '\e802';
          position: relative;
          margin-left: 10px;
          font-family: 'fontello';
          font-size: 9px;
        }
      }
      .less {
        color: #00ba9f;
        background: transparent;
        position: relative;
        &:after {
          content: '\e805';
          position: relative;
          margin-left: 10px;
          font-family: 'fontello';
          font-size: 9px;
        }
      }
      .btn {
        color: #00ba9f;
        background: transparent;
        &:after {
          content: 'v';
          position: relative;
          margin-left: 5px;
        }
      }
    }
  }
  &-bottom {
    padding: 14px 15px;
    border-top: 1px solid rgba($black, 0.2);
    line-height: 26px;
    @include clearfix;
    @media screen and (min-width: $tablet-medium) {
      padding: 14px 30px;
    }

    ul {
      padding: 0;
      margin: 0;
      text-align: center;
      li {
        display: inline-block;
        a {
          display: block;
          padding: 0 10px;
          color: #909195;
          font-size: 15px;
          font-weight: 400;
          @include transition (all .3s ease);
          @include hover {
            color: $red;
          }
        }
      }
    }
  }
}

.page-recherche,
.recherche-agent {
  .footer {
    width: 100%;
    @media screen and (min-width: $desktop-small) {
      float: right;
      width: 50%;
      max-width: 900px;
      left: auto;
    }
    @media screen and (min-width: $desktop-medium) {
      width: 670px;
    }
    .footer-logos {
      display: none;
    }
    .footer-top {
      @media screen and (min-width: $tablet-medium) {
        padding: 20px 30px 0;
        margin-bottom: 30px;
      }
      //.logo {
      //}
      .social {
        width: calc(100% - 90px);
        float: left;
        text-align: right;
        @media screen and (min-width: $tablet-medium) {
          width: calc(100% - 120px);
        }
        @media screen and (min-width: $tablet-medium2) {
          width: 80%;
        }
      }
      .newsletter {
        width: 100%;
        clear: both;
        margin-top: 20px;
      }
    }
    .footer-middle {
      display: block;
      @media screen and (min-width: $desktop-small) {
        display: none;
      }
    }
  }
}
.page-recherche{
  .footer{
    @media screen and (min-width: $desktop-small){
      width: 670px;
    }
  }
}