.clearfix {
    @include clearfix;
}

.main {
    padding-top: 50px;
    height: 100%;
    //min-height:100%;
    //position: relative;
}

.wrapper {
    margin: 0 auto;
    position: relative;
    padding: 0 15px;
    max-width: 1126px;
    @include clearfix;
    //@media screen and (min-width: $desktop-medium + 1) {
    //  max-width: $desktop-medium - 60;
    //}
    @media screen and (min-width: $tablet-medium) {
        padding: 0 30px;
    }
    @media screen and (min-width: $desktop-medium) {
        padding: 0;
    }

}

.wrapper-fullwidth {
    margin: 0 auto;
    position: relative;
    @include clearfix;
}

.SumoSelect {
    width: 100%;

    @include hover {
        > .CaptionCont {
            box-shadow: none;
            border: 1px solid #dddddd;
        }
    }
    > .CaptionCont {
        border-radius: 4px;
        border: 1px solid #dddddd;
        background: $white;
        height: 36px;
        line-height: 22px;
        padding: 5px 12px;
        cursor: pointer;
        > span {
            cursor: pointer;
        }
        > label {
            > i {
                color: #909195;
                font-size: 12px;
                font-style: normal;
                line-height: 16px;
                text-align: center;
                background-image: none;
                @include transition (all .3s ease);
                @include opacity(1);
                &:before {
                    content: '\e802';
                    font-family: 'fontello';
                }
            }
        }
    }
    &.open {
        > .CaptionCont {
            > label {
                > i {
                    &:before {
                        content: '\e805';
                    }
                }
            }
        }
        > .optWrapper {
            top: 40px;
            min-height: 100%;
            opacity: 1;
            z-index: 10;
            height: auto;
        }
    }
    > .optWrapper {
        border-radius: 0;
        border: 1px solid #e5e6e4;
        box-shadow: 0 0 20px rgba(0, 0, 0, .15);
        padding: 10px;
        @include transition (all .3s ease);
        display: block;
        min-height: 0;
        height: 0;
        opacity: 0;
        z-index: -1;
        &:after {
            content: '';
            display: block;
            position: absolute;
            bottom: 8px;
            left: 0;
            right: 0;
            background: $white;
            height: 3px;
            z-index: 2;
        }
        &.multiple {
            > .options {
                li {
                    &.opt {
                        span {
                            i {
                                box-shadow: none;
                                background: $grey;
                                border: 3px solid $grey;
                                width: 16px;
                                height: 16px;
                            }
                        }
                        &.selected {
                            span {
                                i {
                                    box-shadow: none;
                                    background: $color2;
                                    background-image: none;
                                    border: 3px solid $grey;
                                }
                            }
                        }
                    }
                }
            }
        }
        > .options {
            li {
                &.opt {
                    line-height: 23px;
                    @include transition (all .3s ease);
                    @include hover {
                        background: $white;
                        color: $color2;
                    }
                }
            }
        }
        &.isFloating {
            position: absolute;
            top: auto;
            left: auto;
            right: auto;
            width: 100%;
            bottom: auto;
            margin: auto;
            max-height: none;
            > .options {
                box-shadow: none;
            }
        }
    }
}

.owl-theme {
    .owl-nav {
        [class*=owl-] {
            margin: 0;
            color: $textcolor-1;
            padding: 0;
            @include transition (all .3s ease);
            @include hover {
                background: $light-grey;
            }
        }
        //.owl-prev{
        //
        //}
        //.owl-next{
        //
        //}
    }
    .owl-dots {
        .owl-dot {
            vertical-align: middle;
            &.active {
                span {
                    background: $red;
                    width: 10px;
                    height: 10px;
                }
            }
            span {
                background: $light-grey;
                width: 6px;
                height: 6px;
            }
        }
    }
}

.wrap-back {
    background: $color3;
    border-bottom: 1px solid $color1;
    padding: 37px 0 0;
    z-index: 10;
    position: relative;
    @media screen and (min-width: $desktop-small) {
        position: fixed;
        left: 0;
        right: 0;
        top: 50px;
    }
    &.fixed {
        position: fixed;
        left: 0;
        right: 0;
        top: 50px;
        //@include box-shadow(0, 0, 10px, 0, rgba($black, 0.5));
    }
    .btn-back {
        background: $light-grey;
        color: $white;
        font-weight: 400;
        height: 36px;
        line-height: 36px;
        padding: 0 15px;
        display: inline-block;
        vertical-align: middle;
        border-radius: 6px 6px 0 0;
        i {
            margin-right: 5px;
            font-size: 11px;
            top: -2px;
            position: relative;
        }
    }
}

.row {
    position: relative;
    @include clearfix;
    @media screen and (min-width: $desktop-small) {
        margin-right: -15px;
        margin-left: -15px;
    }
}

.side-left {
    position: relative;
    @include clearfix;
    @media screen and (min-width: $desktop-small) {
        width: calc(100% - 350px);
        float: left;
        padding-left: 15px;
        padding-right: 15px;
    }
}

.side-right {
    position: relative;
    @include clearfix;
    @media screen and (min-width: $desktop-small) {
        width: 350px;
        float: left;
        padding-left: 15px;
        padding-right: 15px;
    }
}

.form-group {
    margin-bottom: 15px;
    label {
        margin-bottom: 5px;
        display: block;
    }
    .form-control {
        border: 1px solid $color1;
        padding: 10px 20px;
        border-radius: 4px;
        background: $white;
        font-weight: 500;
        font-size: 14px;
        width: 100%;
        resize: none;
        @include placeholder {
            font-style: italic;
            color: #909195;
        }
    }
}

.animated2 {
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@-webkit-keyframes fadeInDown2 {
    0% {
        opacity: 1;
        -webkit-transform: translateY(-100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
    }
}

@-moz-keyframes fadeInDown2 {
    0% {
        opacity: 1;
        -moz-transform: translateY(-100%);
    }

    100% {
        opacity: 1;
        -moz-transform: translateY(0);
    }
}

@-o-keyframes fadeInDown2 {
    0% {
        opacity: 1;
        -o-transform: translateY(-100%);
    }

    100% {
        opacity: 1;
        -o-transform: translateY(0);
    }
}

@keyframes fadeInDown2 {
    0% {
        opacity: 1;
        transform: translateY(-100%);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.fadeInDown2 {
    animation-name: fadeInDown2;
}

.progress {
    height: 4px;
    overflow: hidden; // force rounded corners by cropping it
    font-size: 14px;
    background-color: $color1;
    @include flex;
}

.progress-bar {
    flex-direction: column;
    justify-content: center;
    color: $white;
    text-align: center;
    background-color: $color4;
    @include transition(width .6s ease);
    @include flex;
}

.label-radio {
    margin-top: 15px;
}

.group-radio {
    margin-bottom: 15px;
    margin-top: 10px;
}

.checkbox {
    input[type='checkbox'],
    input[type='radio'] {
        @include hover {
            outline: none;
            & + label {
                &:before {
                    outline: none;
                    outline-offset: 0;
                }
            }
        }
    }
}

.abc-radio {
    &.form-check-inline {
        display: inline-block;
        margin-right: 20px;
        margin-bottom: 15px;
    }
    input[type=radio] {
        @include hover {
            outline: none;
            & + label {
                &:before {
                    outline: none;
                    outline-offset: 0;
                }
            }
        }
    }
    label {
        font-size: 15px;
        font-weight: 600;
        &:after {
            background: $color2;
        }
    }
}

.show-on-mobile {
    @media screen and (min-width: $tablet-medium) {
        display: none;
    }
}

.show-on-tablet {
    @media screen and (min-width: $desktop-small) {
        display: none;
    }
}

//backend login
body {
    &.toolbar-fixed {
        overflow: hidden;
    }

    &.page-home {
        overflow: hidden;
    }
}

.toolbar-fixed {
    .header {
        top: 79px;
        &.fixed {
            top: 79px;
        }
    }
    .content-home__left {
        .inner {
            &:before {
                content: '';
                width: 100%;
                height: 100%;
                background-image: linear-gradient(180deg, rgba(43, 43, 43, 0.7), rgba(107, 107, 107, 0.4));
                top: 0;
                left: 0;
                z-index: 13; 
                position: absolute;
            }
        }
    }
}

//popup
#popup_box {
    display: block;
    position: fixed;
    z-index: 999;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    #popupBoxClose {
        background: url('../images/i-close.png') no-repeat;
        width: 36px;
        height: 37px;
        position: absolute;
        top: 0;
        right: 0;
        text-indent: -999px;
        padding: 10px;
        background-position: center center;
        cursor: pointer;
    }
    .popup_box_main {
        margin: 0 auto;
        width: 100%;
        height: 200px;
        display: block;
        position: absolute;
        left: 0;
        top: 50%;
        right: 0;
        bottom: 0;
        @include transform(translateY(-50%));
        padding: 30px;
        background: $color3;
        border-radius: 4px;
        overflow-y: auto;
        @media screen and (min-width: $tablet-medium) {
            max-width: 500px;
            height: 200px;
        }
        #block-sharethiswidget-2 {
            margin: 0 auto;
            text-align: center;
            position: absolute;
            left: 0;
            top: 50%;
            right: 0;
            @include transform(translateY(-50%));
        }
        .sharethis-wrapper {
            text-align: center;
            position: absolute;
            left: 0;
            top: 50%;
            right: 0;
            @include transform(translateY(-50%));
        }
    }
}